<template>
  <div class="digitalvenuedetail">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
        <ul>
          	<li  @click="tourl('/index')"><a>首页</a></li>
          	<li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
          		<li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
          	<li class="active" @click="tourl('/digitalvenue')"><a>数字传播</a></li>
          	<li @click="tourl('/venue')"><a>革命场馆</a></li>
          	<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
        </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>

    <div class="detail-bread">
      <div class="w1400">
        <div class="detail-bread-ul">
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>数字传播</span>
          <span>/</span>
          <span>{{alllist.category_name}}</span>
          <span>/</span>
          <span>详情</span>
        </div>
        <div class="detail-title">
          <p class="detail-title-1">{{alllist.title}}</p>
          <p class="detail-title-2">{{alllist.add_time}}</p>
        </div>
        <div class="detail-video">
          <video controls="controls" :src="alllist.video"></video>
        </div>
      </div>
    </div>
    <div class="detail-tuijian">
      <div class="w1400">
        <div class="digitalvenue-title">
          <div class="digitalvenue-title-left">
            <span class="digitalvenue-title-1">{{alllist.category_name}}</span>
          </div>
          <div class="digitalvenue-title-right" @click="gotoxq(alllist.category_id)">查看更多</div>
        </div>
        <div class="digitalvenue-ul">
          <ul class="flex flex-row flex-wrap align-start justify-start">
            <li v-for="(item, indexs) in alllist.otherVideo" @click="gotoDt(item.id)">
              <div class="digitalvenue-ul-img">
                <!-- <img :src="item.cover" > -->
                <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'">

                </div>
                <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
                    <p>收藏</p>
                  </span><span class="el-icon-star-on" v-else>
                    <p>已收藏</p>
                  </span></div>
              </div>
              <div class="digitalvenue-ul-title">
                <p>{{item.title}}</p>
                <img src="../assets/img/dig-play.png">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  export default {
    name: 'digitalvenuedetail',
    data() {
      return {
        id: '',
        alllist: {},
        showli: true
      }
    },
    mounted() {
      this.id = this.$route.query.id
      this.getlist()
    },
    methods: {
      gotoxq(id) {
        this.$router.push({
          path: '/digitalvenuelist',
          query: {
            id: id
          }
        });
      },
      gotoDt(id) {
        // this.id = id
        // this.getlist()
      },
      shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },
    tourl(url){
      let routeUrl = this.$router.resolve({
                path:url,
              }) // 带参跳转

              window.open(routeUrl.href, '_blank');
    },
      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'digitalvenue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'digitalvenue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      getlist() {
        var that = this
        this.$http
          .post(api.user.VideoInfo, {
            video_id: this.id,
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              that.alllist = res.data.data

            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
    },
  }
</script>

<style scoped>
  ul li{
    cursor: pointer;
  }
  .digitalvenuedetail {
    width: 100%;
    height: auto;
  }

  .detail-bread {
    width: 1400px;
    margin: 0 auto;
    padding-top: 100px;
    padding-bottom: 60px;
  }

  .detail-bread-ul {
    width: 100%;
    height: 80px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  .detail-bread-ul span {
    font-size: 16px;
    color: #999;
    padding: 0 5px;
    display: inline-block;
  }

  .detail-bread-ul span a {
    color: rgba(153, 153, 153, .5);
  }

  .detail-bread-ul span a:hover {
    color: #c71925;
  }

  .detail-title {
    width: 100%;
    padding-bottom: 30px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  .detail-title-1 {
    max-width: calc(100% - 200px);
    font-size: 36px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
  }

  .detail-title-2 {
    width: 130px;
    font-size: 20px;
    color: rgba(117, 117, 117, .8);
    background: url(../assets/img/calendar.png)no-repeat left center;
    background-size: 18px 18px;
    padding-left: 35px;
    margin-left: 35px;
  }

  .detail-video {
    width: 1400px;
    height: 780px;
    overflow: hidden;
  }

  .detail-video video {
    width: 100%;
    height: 100%;
	display: block !important;
  }

  .detail-tuijian {
    width: 100%;
    background-color: #fff;
  }

  .digitalvenue-title {
    width: 100%;
    padding-top: 30px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-title-left {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
  }

  .digitalvenue-title-1 {
    font-size: 30px;
    color: #000;
  }

  .digitalvenue-title-right {
    background: url(../assets/img/dig-more.png) no-repeat right center;
    background-size: 14px 19px;
    padding-right: 30px;
    margin-right: 10px;
    font-size: 18px;
    cursor: pointer;
  }

  .digitalvenue-title-right a {
    color: #c71925;
  }

  .digitalvenue-title-right a:hover {
    color: #c71925;
    opacity: .9;
  }

  .digitalvenue-ul {
    width: 100%;
    border-bottom: 1px solid #adadad;
    padding-bottom: 40px;
  }

  .digitalvenue-ul ul li {
    width: 23.5%;
    margin-right: 2%;
    overflow: hidden;
    margin-top: 30px;
    cursor: pointer;
  }

  .digitalvenue-ul ul li:nth-child(4n) {
    margin-right: 0px;
  }

  .digitalvenue-ul-img {
    width: 100%;
    height: auto;
    position: relative;
  }

  .digitalvenue-ul-img img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-img .fm {
    width: 100%;
    height: 235px;
    background-size: cover !important;

  }

  .digitalvenue-ul-img:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .digitalvenue-ul-title {
    width: 100%;
    height: 60px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-ul-title p {
    width: calc(100% - 24px);
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .digitalvenue-ul-title img {
    width: 24px;
    height: 24px;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;

  }

  .digitalvenue-li-img:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF;
    font-size: 16px;
  }
</style>



