                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="password" :rules="[{required:true,message:'密码不能为空'}]">
                  <el-input v-model="loginForm.password" type="password" placeholder="请输入密码" show-password></el-input>
                </el-form-item>
              </el-col>
              <!--          <el-col :span="24" class="flex flex-row flex-nowrap align-start justify-between">
                <el-form-item label="" prop="vcode" :rules="[{required:true,message:'验证码不能为空'}]">
                  <el-input v-model="loginForm.vcode" placeholder="验证码"></el-input>
                </el-form-item>
                <div class="formvcode"><img src="../assets/img/yzm.jpg"></div>
              </el-col> -->
              <el-col :span="24" class="forgotpassword flex align-center justify-end">
                <div @click="gowj()">忘记密码？</div>
              </el-col>
            </el-row>
            <el-form-item class="formbtn">
              <el-button @click="saveLogin">登陆</el-button>
            </el-form-item>
          </el-form>
        </div>
        <!-- 注册模块 -->
        <div class="registerform" v-if="isRegister">
          <el-form ref="registerForm" :rules="registerrules" :model="registerForm" label-width="0px">
            <el-row>
              <el-col :span="24">
                <el-form-item label="" prop="tel">
                  <el-input v-model="registerForm.tel" placeholder="请输入手机号"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24" class="flex flex-row flex-nowrap align-start justify-between">
                <el-form-item label="" prop="sendCode" >
                  <el-input v-model="registerForm.sendCode" placeholder="请输入验证码"></el-input>
                </el-form-item>
                <div class="yam-btn">
                  <button @click="sendMsg" v-if="sendDisabled">发送验证码</button>
                  <button disabled="true" v-else>{{sendTime+"s"}}</button>
                </div>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="password">
                  <el-input v-model="registerForm.password" type="password" placeholder="8-20位密码，字母/数字/符号至少2种"
                    show-password minlength="8" maxlength="20"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="againpassword">
                  <el-input v-model="registerForm.againpassword" type="password" placeholder="请再次输入密码" show-password
                    minlength="8" maxlength="20"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-checkbox v-model="checked">我已阅读并接受<span @click="openUserBox">用户协议</span>和<span @click="openUserBox">隐私政策</span></el-checkbox>
              </el-col>
            </el-row>
            <el-form-item class="formbtn" v-if="!no">
              <el-button @click="saveRegister">注册</el-button>
            </el-form-item>

            <el-form-item class="formbtn" v-else>
              <el-button @click="saveRegister">重置密码</el-button>
            </el-form-item>
          </el-form>
        </div>
      </div>
    </div>

    <div class="dingzhi-bg" v-if="isUserBox == true">
      <div class="dingzhi">
        <div class="bg-white">
          <h2>隐私协议</h2>
          <div class="userbox">
            <el-scrollbar style="height:100%">
              <p>我们一向庄严承诺保护使用户（“用户”或“您”）的隐私。您在使用我们服务时，我们可能会收集和使用您的相关信息。</p>
              <p>本《隐私政策》适用于我们提供的一切我们服务。当您使用我们任何单项服务时，您同意接受本《隐私政策》以及我们在该单项服务中发出的特定隐私信息类政策条款（下列称“特定条款”）的保护，在此情况下特定条款与本政策条款同时对您产生效力。例如：使用我们在线游戏服务的用户，同时适用本《隐私政策》以及《网络游戏用户隐私权保护和个人信息利用政策》（见附件一）；又如，使用我们支付的用户，同时适用本《隐私政策》以及《我们支付隐私政策》（见附件二）。如特定条款与本《隐私政策》条款存在同类条款的不一致约定，则在特定条款约束范围内应以特定条款为准。如我们提供的某一单项服务不适用本《隐私政策》的，该服务中会以适当方式明示排除适用本《隐私政策》。</p>
              <p>请注意我们不时地会检查我们的政策，因此有关的措施会随之变化。我们恳请您定期光顾本页面，以确保对我们《隐私政策》最新版本始终保持了解。在阅读完本政策之后，如您对本《隐私政策》或与本《隐私政策》相关的事宜有任何问题，请与我们联系。</p>
              <p>您使用或继续使用我们服务，都表示您同意我们按照本《隐私政策》收集、使用、储存和分享您的信息。</p>
              <p>目录</p>
              <p>一、我们可能收集的信息</p>
              <p>二、我们如何收集和使用信息</p>
              <p>三、我们可能分享、转让和披露的信息</p>
              <p>四、我们如何保留、储存和保护信息</p>
              <p>五、如何管理您的信息</p>
              <p>六、第三方服务</p>
              <p>七、年龄限制</p>
              <p>八、通知和修订</p>
              <p>九、如何联系我们</p>
              <p>一、我们可能收集的信息</p>
              <p>（一）与个人身份无关的信息：</p>
              <p>当您使用我们服务时，我们可能收集和汇总诸如用户的来源途径、访问顺序等信息，例如记录使用我们服务的每个用户的来源途径、浏览器软件等。</p>
              <p>（二）有关个人身份的信息：</p>
              <p>当您使用我们服务时，我们可能收集和汇总或要求您提供有关个人身份的信息，例如个人身份证明（包括身份证、护照、驾驶证等）；生日、籍贯、性别、兴趣爱好、个人电话号码、面部特征；网络身份标识信息（包括系统账号、IP地址、电子邮箱地址及与前述有关的密码、口令、口令保护答案等）；个人财产信息（交易记录、余额、优惠券、游戏类兑换码、银行卡信息等）；通讯信息；个人上网记录和日志信息（包括网站浏览记录、软件使用记录、点击记录、操作日志等）；设备信息（包括设备型号、设备MAC地址、操作系统类型、设备设置）；软件列表唯一设备识别码（如IMEI/android
                ID/IDFA/OPENUDID/GUID、SIM卡IMSI</p>
            </el-scrollbar>
          </div>
          <div class="dingzhi-close" @click="isUserBox = false"><i class="el-icon-close text-c72c2c"></i></div>
        </div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
