<script>
  import api from "../api/api.js";
  import {
    Message
  } from 'element-ui';
  export default {
    name: 'user',
    data() {
      var validatePass = (rule, value, callback) => {
        if (value === '') {
          callback(new Error('请再次输入密码'));
        } else if (value !== this.registerForm.password) {
          callback(new Error('两次输入密码不一致!'));
        } else {
          callback();
        }
      };
      return {
        isLogin: true,
        isRegister: false,
        loginForm: {
          name: '',
          password: '',
          // vcode: ''
        },
        registerForm: {
          tel: '',
          sendCode: '',
          password: '',
          againpassword: '',
          vcode: ''
        },
        registerrules: {
          tel: [{
            required: true,
            message: '请输入密码手机号'
          }],
          sendCode: [{
            required: true,
            message: '请输入验证码'
          }],
          password: [{
            required: true,
            message: '请输入密码'
          }, {
            min: 8,
            max: 20,
            message: '长度在 8 到 20 个字符'
          }],
          againpassword: [{
            min: 8,
            max: 20,
            message: '长度在 8 到 20 个字符'
          }, {
            validator: validatePass,
            trigger: 'blur'
          }],
        },
        sendDisabled: true, // 手机验证码
        sendTime: 60, // 倒计时间
        checked: false, // 勾选协议
        isUserBox: false, // 协议弹框
        no:false
      }
    },
    mounted() {

    },
    methods: {
      tourl(url){
        let routeUrl = this.$router.resolve({
                  path:url,
                }) // 带参跳转

                window.open(routeUrl.href, '_blank');
      },
      gowj(){
        this.no = true
        this.isRegister =true
      },
      // 获取验证码
      sendMsg: function() {
        let that = this;
        if (that.registerForm.tel === '') {
          Message({
            message: '请输入手机号码',
            type: 'error',
            center: true,
            offset: 130,
            duration: 1000
          });
          return false;
        } else if (!(/^1[3456789]\d{9}$/.test(that.registerForm.tel))) {
          Message({
            message: '请填写正确的手机号',
            type: 'error',
            center: true,
            offset: 130,
            duration: 1000
          });
          return false;
        }
        if (that.sendDisabled == false) {
          return false
        } else {
          that.send()
        }
        that.countdown()
        that.sendDisabled = false;
      },

      // 登录
      saveLogin() {
        this.$refs['loginForm'].validate((valid) => {
          if (valid) {
            var data = this.loginForm
            this.$http
              .post(api.user.UserLogin, {
                username: data.name,
                password: data.password
              })
              .then((res) => {
                if (res.data.code == 0) {
                  localStorage.setItem("token", res.data.data.token);
                   localStorage.setItem("userinfo", JSON.stringify(res.data.data.userinfo));
                  if (localStorage.getItem("token")) {
                    this.$router.push({
                      path: '/index'
                    });
                  }
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: "error",
                  });
                }
              });
          } else {
            return false
          }
        })
      },
    gb(){
      this.isRegister = !this.isRegister,
      this.no = false
       this.$refs['registerForm'].resetFields()
    },

      // 注册
      saveRegister() {

        this.$refs['registerForm'].validate((valid) => {
          if (valid) {
            if (!this.checked) {
              this.$message({
                message: '请同意协议',
                type: "error",
              });
              return false
            }
             var data = this.registerForm
             console.log(this.registerForm,"jhh")
            if(this.no){
              var that = this
              this.$http
                .post(api.user.ForgetPassword, {
                  phone: that.registerForm.tel,
                  setPasscode: that.registerForm.sendCode,
                  password:that.registerForm.password,
                  reqirPass:that.registerForm.againpassword,
                })
                .then((res) => {
                  if (res.data.code == 0) {
                        this.$message({
                          message: '重置成功',
                          type: "success",
                        });
                        that.no = false
                        that.isRegister = false
                        that.$refs['registerForm'].resetFields()

                  } else {
                    this.$message({
                      message: res.data.res,
                      type: "error",
                    });
                  }
                });


              return false
            }
            this.$http
              .post(api.user.UserRegister, {
                password: data.password,
                phone: data.tel,
                affirmPass: data.againpassword,
                verifCode: data.sendCode,
              })
              .then((res) => {
                if (res.data.code == 0) {
                  localStorage.setItem("token", res.data.data.token);
                                     localStorage.setItem("userinfo", JSON.stringify(res.data.data.userInfo));
                  if (localStorage.getItem("token")) {
                    this.$router.push({
                      path: '/index'
                    });
                  }
                } else {
                  this.$message({
                    message: res.data.msg,
                    type: "error",
                  });
                }
              });
          } else {
            return false
          }
        })
      },
      // 发送验证码
      send() {
        var name = ''
        if(!this.no){
          name = 'register'
        }else{
          name = 'setPass'
        }
        var that = this
        this.$http
          .post(api.user.SendMsgCode, {
            phone: that.registerForm.tel,
            scene: name
          })
          .then((res) => {
            if (res.data.code == 0) {
              this.$message({
                message: '发送成功',
                type: "success",
              });
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      // 协议弹框
      openUserBox() {
        this.isUserBox = true;
      },
      // 倒计时
      countdown: function() {
        var promise = new Promise((resolve, reject) => {
          var setTimer = setInterval(() => {
            this.sendTime--
            if (this.sendTime <= 0) {
              this.sendTime = 60;
              this.sendDisabled = true
              resolve(setTimer)
            }
          }, 1000)
        })
        promise.then((setTimer) => {
          clearInterval(setTimer)
        })
      },
    },
  }
</script>

<style scoped>
  .user {
    width: 100vw;
    background-color: #fff;
  }
  ul li{
    cursor: pointer;
  }
  .user-left {
    width: 50%;
    display: flex;
    align-items: center;
    justify-content: center;
  }

