    border-color: #cbcbcb;
  }

  .user-right>>>.el-form-item.is-error .el-input__inner {
    border-color: #F56C6C;
  }

  .formvcode {
    width: 140px;
    height: 52px;
    overflow: hidden;
  }

  .forgotpassword {
    height: 30px;
    cursor: pointer;
  }

  .forgotpassword div {
    color: #666;
    font-size: 18px;
  }

  .forgotpassword div:hover {
    color: #c72c2c;
  }

  .formbtn {
    padding-top: 30px;
  }

  .user-right>>>.el-button {
    width: 100%;
    height: 65px;
    font-size: 24px;
    color: rgba(0, 0, 0, 0.5);
    border: 0px;
    background-color: #dbdbdb;
  }

  .user-right>>>.el-button:hover {
    background-color: #c72c2c;
    color: #fff;
  }

  /* 注册样式 */
  .registerform {
    width: 410px;
    padding-top: 50px;
  }

  .yam-btn button {
    width: 150px;
    height: 52px;
    background-color: #dbdbdb;
    color: rgba(131, 137, 153, .6);
    font-size: 18px;
    border: 0;
    cursor: pointer;
  }

  .yam-btn button:hover {
    color: #838999;
  }

  .user-right>>>.el-checkbox {
    width: 100%;
  }

  .user-right>>>.el-checkbox .el-checkbox__label {
    font-size: 16px;
  }

  .user-right>>>.el-checkbox .el-checkbox__label span {
    color: #fd9f36;
    display: inline-block;
    padding: 0 5px;
  }

  .user-right>>>.el-checkbox .el-checkbox__label span:hover {
    color: #c72c2c;
  }

  .user-right>>>.el-checkbox__inner {
    width: 16px;
    height: 16px;
  }

  .user-right>>>.el-checkbox__inner::after {
    left: 5px;
    top: 2px;
  }

  .user-right>>>.el-checkbox__input.is-checked+.el-checkbox__label {
    color: #000;
  }

  /* 弹框样式 */
  .dingzhibox {
    width: 290px;
    height: 90px;
    margin: 70px auto;
    background-color: #c72c2c;
    font-size: 30px;
    color: #fff;
    cursor: pointer;
  }

  .dingzhi-bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.8);
  }

  .dingzhi {
    width: 980px;
    height: auto;
    border: 10px solid rgba(199, 44, 44, 0.5);
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 10vh;
    left: calc(50% - 490px);
  }

  .dingzhi .bg-white {
    width: 780px;
    border: 2px solid #c72c2c;
    padding: 40px 98px;
  }

  .dingzhi .bg-white h2 {
    width: 100%;
    text-align: center;
    margin-top: 0;
  }

  .dingzhi-close {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .dingzhi-close i {
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
  }

  .userbox {
    width: 100%;
    height: 500px;
  }

  .dingzhi>>>.el-scrollbar {
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    margin-top: 30px;
  }

  .dingzhi>>>.el-scrollbar__wrap {
    overflow-x: hidden;
    overflow-y: scroll;
    width: calc(100% + 17px);
    height: 100%;
  }

  .dingzhi>>>.el-scrollbar__view {
    padding-right: 17px;
  }

  .dingzhi>>>.el-scrollbar__bar.is-vertical {
