<template>
	<div class="groupexhibition">
		<div class="new_header header_brotop active">
			<div class="flex flex-row flex-nowrap align-center space-between w1400">
				<div class="logo" ref="logo"></div>
				<div class="menu-bar">
					<ul>
						<li @click="tourl('/index')"><a>首页</a></li>
						<li  @click="tourl('/groupexhibition')"><a>数字联展</a></li>
						<li class="active" @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
						<li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
						<li @click="tourl('/venue')"><a>革命场馆</a></li>
						<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
					</ul>
				</div>
				<!-- <div class="new_user"></div> -->
			</div>
		</div>
		<div class="bread_crumbs flex flex-nowrap">
			<div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
			<div class="br_line"></div>
			<div class="br_right">
				<h2>数字联展</h2>
				<div class="br_ul">
					<span><img src="../assets/img/crumbs_2.png"></span>
					<span>
						<router-link to="/index">首页</router-link>
					</span>
					<span>/</span>
					<span>数字联展</span>
				</div>
			</div>
		</div>

		<div class="groupexhibitionnews">
			<div class="w1400">
				<div class="groupexhibitionnews-search flex flex-row flex-nowrap align-center justify-between">
					<el-select v-model="pr" placeholder="省份: 全部" @change='getindex(pr)'>
						<el-option v-for="(item,index) in sflist" :key="index" :label="item.name" :value="item.name">
						</el-option>
					</el-select>
					<el-select v-model="city" placeholder="城市: 全部" @change='getindex2(city)'>
						<el-option v-for="(item,index) in citylist" :key="index" :label="item.name" :value="item.name">
						</el-option>
					</el-select>

					<el-date-picker style="width: 240px;" v-model="time" type="daterange" range-separator="至" start-placeholder="开始日期"
						end-placeholder="结束日期">
					</el-date-picker>


					<el-input class="search-val" v-model="input" placeholder="名称"></el-input>
					<el-button @click='getList'>搜索</el-button>
				</div>

				<div class="groupexhibitionnews-list">
					<ul>
						<li v-for="(item, index) in list" @click="goToDetail(item.id,item.title)"
							class="flex flex-row flex-nowrap align-center justify-between">
							<div class="gen-list-left">
								<p>{{item.create_time[2]}}</p>
								<p>&nbsp;</p>
								<p>{{item.create_time[0]}}.{{item.create_time[1]}}</p>
								<span>&nbsp;</span>
							</div>
							<div class="gen-list-center flex flex-direction justify-around">
								<p>{{item.title}}</p>
								<p>{{item.digest}}</p>
							</div>
							<div class="gen-list-right">&nbsp;</div>
						</li>
					</ul>
					<div class="showFY">
					  <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />
					
					</div>
				</div>
			</div>
		</div>

		<div class="new_footer">
			<div class="new_foot">
				<div class="new_foot_left">
					<ul>
						<li>
							<router-link to="/index">红色足迹</router-link>
						</li>
						<li>|</li>
						<li>
							<router-link to="/groupexhibition">数字联展</router-link>
						</li>
						<li>|</li>
						<li>
														<router-link to="/groupexhibitionnews">联展资讯</router-link>
						</li>
						<li>|</li>
						<li>
							<router-link to="/digitalvenue">数字联播</router-link>
						</li>
						<li>|</li>
						<li>
							<router-link to="/venue">革命场馆</router-link>
						</li>
						<li>|</li>
						<li>
							<router-link to="/user">个人中心</router-link>
						</li>
					</ul>
					<p class="new_foot_line"></p>
					<p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
				</div>
				<div class="new_foot_right">
					<div class="foot_weixin">
						<!-- <router-link to="/index"></router-link> -->
						<div class="showlg"></div>
						<div class="show_div">
							<img src="../assets/img/ewm.png" alt="">
						</div>
					</div>
					<div class="foot_tel">
						<!-- <router-link to="/index"></router-link> -->
						<div class="showlg"></div>
						<div class="show_div">
							待更新
						</div>
					</div>
				</div>
			</div>
		</div>
	</div>
</template>

<script>
	import TablePaging from "../components/Fy";
	import api from "../api/api.js";
	import $ from 'jquery';
	export default {
		name: 'groupexhibition',
		components: {
		
		  TablePaging,
		},
		data() {
			return {
				pageIndex: 0,
				isExhibit: false,
				options: [{
					value: '选项1',
					label: '黄金糕'
				}, {
					value: '选项2',
					label: '双皮奶'
				}, {
					value: '选项3',
					label: '蚵仔煎'
				}],
				value: '',
				input: '',
				pr: null,
				city: null,
				sflist: [],
				citylist: [],
				time: {},
				currentPage: 1,
				total: 0,
				pageSize: 9,
				provinceid: '',
				cityid: '',
				list: [],
				showli: true
			}
		},
		mounted() {
			$('.groupexhibitionnews-list ul li').hover(
				function() {
					$(this).addClass('active');
				},
				function() {
					$(this).removeClass('active');
				}
			);
			this.getSF()
			this.getList()
		},
		methods: {
    getList() {
        var that = this
        this.$http
          .post(api.user.ArticleList, {
             type:1,
            province: this.provinceid,
            city: this.cityid,
            keyword: this.input,
            limit: this.pageSize,
            page: this.currentPage,
            start: this.time[0],
            end: this.time[1]
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              that.list = res.data.data.List
              that.total = res.data.data.totalNum

            }else if(res.data.code == -100){
              localStorage.clear();
              that.getList()
			 that.showli = false
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
			// 分页
			pageChange(value) {
			  console.log(value, "hhhh");
			  this.currentPage = value;
			  this.getList()
			},
			getindex(item) {
			  console.log(item)
			  this.pr = item
			  this.sflist.forEach((i, x) => {
			    if (i.name == item) {
			      this.city = ''
			      this.citylist = i.city
			      this.provinceid = i.id
			    }
			  });
			  console.log(this.provinceid)
			},
			getindex2(item) {
			  this.city = item
			  this.citylist.forEach((i, x) => {
			    if (i.name == item) {
			      this.cityid = i.id
			    }
			  });
			  console.log(this.cityid)
			},
			// 跳转详情
			goToDetail(id,title) {
				// this.$router.push({
				// 	path: '/politicsclassnewsdetail'
				// });
				    this.$router.push({ path: '/politicsclassnewsdetail', query:{ id: id,title:title}});
			},
			tourl(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}) // 带参跳转

				window.open(routeUrl.href, '_blank');
			},
			getSF() {
				this.$http
					.post(api.user.sf, {

					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {

							this.sflist = res.data.data
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
		},
	}
</script>

<style scoped>
	ul li {
		cursor: pointer;
	}

	.groupexhibition {
		width: 100%;
		height: auto;
		background: #fff url(../assets/img/groupexhibition-bg.jpg) no-repeat center top;
		background-size: 100% 428px
	}

	.bread_crumbs {
		width: 1400px;
		margin: 0 auto;
		padding-top: 190px
	}
  .showFY {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    /* margin-bottom: 50px; */
	padding-bottom: 50px;
    margin-top: 50px;
  }

	.groupexhibitionnews {
		width: 100%;
		margin-top: 90px;
		background-color: #f5f5f5;
	}

	.groupexhibitionnews-search {
		width: 100%;
		padding: 40px 0;
	}

	.groupexhibitionnews-search>>>.el-select {
		width: 15%;
	}

	.groupexhibitionnews-search>>>.search-val {
		width: 40%;
	}

	.groupexhibitionnews-search>>>.el-button {
		width: 10%;
	}

	.groupexhibitionnews-list {
		width: 100%;
	}

	.groupexhibitionnews-list ul li {
		width: 100%;
		height: 180px;
		margin-bottom: 20px;
		background-color: #fff;
		cursor: pointer;
	}

	.gen-list-left {
		width: 9%;
		height: 100%;
		color: #fff;
		background-color: #e4e4e4;
		display: flex;
		flex-direction: column;
		align-items: flex-start;
		justify-content: center;
		padding-left: 5%;
		position: relative;
	}

	.gen-list-left p:nth-child(1) {
		font-size: 48px;
	}

	.gen-list-left p:nth-child(2) {
		width: 15px;
		height: 2px;
		background-color: #fff;
		margin: 5px 0 10px;
	}

	.gen-list-left p:nth-child(3) {
		font-size: 18px;
	}

	.gen-list-left::before {
		content: '';
		position: absolute;
		top: 50%;
		right: 0;
		z-index: 1;
		width: 0;
		height: 0;
		border-top: 10px solid transparent;
		border-right: 10px solid #fff;
		border-bottom: 10px solid transparent;
		margin-top: -10px;
	}

	.gen-list-center {
		width: calc(76% - 60px);
		height: calc(100% - 60px);
		padding: 30px;
	}

	.gen-list-center p {
		width: 100%;
		white-space: nowrap;
		text-overflow: ellipsis;
		overflow: hidden;
	}

	.gen-list-center p:nth-child(1) {
		font-size: 24px;
		color: #000;
	}

	.gen-list-center p:nth-child(2) {
		font-size: 16px;
		color: #666;
	}

	.gen-list-right {
		width: 10%;
		height: 100%;
		background: url(../assets/img/pol-more.png) no-repeat center center;
		background-size: 28px 7px;
	}

	.groupexhibitionnews-list ul li.active .gen-list-left {
		background-color: #b5001f;
	}

	.groupexhibitionnews-list ul li.active .gen-list-right {
		background: url(../assets/img/pol-more-active.png) no-repeat center center;
	}
</style>



