<template>
  <div class="venue">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
         <ul>
           	<li  @click="tourl('/index')"><a>首页</a></li>
           	<li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
           		<li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
           	<li class="active" @click="tourl('/digitalvenue')"><a>数字传播</a></li>
           	<li @click="tourl('/venue')"><a>革命场馆</a></li>
           	<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
         </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>数字传播</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span><router-link to="/index">首页</router-link></span>
          <span>/</span>
          <span><router-link to="/digitalvenue">数字传播</router-link></span>
          <span>/</span>
          <span>红色印记</span>
        </div>
      </div>
    </div>

    <div class="digitalvenuelist">
      <div class="w1400" v-if="alllist != undefined && alllist != null && alllist.length > 0 ">
        <div class="digitalvenue-title">
          <div class="digitalvenue-title-left">
            <span class="digitalvenue-title-1">{{listname}}</span>
            <span class="digitalvenue-title-2">共有<span class="text-c72c2c">{{num}}</span>个视频</span>
          </div>
        </div>
        <div class="digitalvenue-ul">
          <ul class="flex flex-row flex-wrap align-start justify-start">
            <li v-for="(item, index) in alllist" >
              <div class="digitalvenue-ul-img">
                <!-- <img :src="item.cover" > -->
                     <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'" @click="goTodetail(item.id)">

                     </div>
                     <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
                         <p>收藏</p>
                       </span><span class="el-icon-star-on" v-else>
                         <p>已收藏</p>
                       </span></div>
                </div>
              <div class="digitalvenue-ul-title" @click="goTodetail(item.id)">
                <p>{{item.title}}</p>
                <img src="../assets/img/dig-play.png">
              </div>
            </li>
          </ul>
          <div class="page flex flex-row flex-nowrap align-center justify-center">
         <!--   <div class="page-prev" v-if="NowPage > 0" @click="left()"><img src="../assets/img/prev.png" ></div>
            <p :class="NowPage == index?'active':''" v-for="(item, index) in pageIndex" :key="index" @click="pageClick(index)">{{index+1}}</p>
            <div class="page-next" v-if="NowPage < 2 &&  pageIndex  != 1" @click="next()"><img src="../assets/img/next.png" ></div> -->
            <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />
          </div>
        </div>
      </div>
			<div class="w1400" v-else>
				<div class="digitalvenue-title">
					
				</div>
			              <div class="nolist">暂无数据</div>
			</div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
       <ul>
         <li>
           <router-link to="/index">红色足迹</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/groupexhibition">数字联展</router-link>
         </li>
         <li>|</li>
         <li>
           							<router-link to="/groupexhibitionnews">联展资讯</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/digitalvenue">数字联播</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/venue">革命场馆</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/user">个人中心</router-link>
         </li>
       </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
     <div class="new_foot_right">
       <div class="foot_weixin">
         <!-- <router-link to="/index"></router-link> -->
         <div class="showlg"></div>
         <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
         </div>
       </div>
       <div class="foot_tel">
         <!-- <router-link to="/index"></router-link> -->
               <div class="showlg"></div>
            <div class="show_div">
                  待更新
            </div>
       </div>
     </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
 import api from "../api/api.js";
   	import TablePaging from "../components/Fy";
export default {
  name: 'digitalvenuelist',
  components: {

  	TablePaging,
  },
  data () {
    return {
      pageIndex: 0,//总数
      NowPage:0,//页数
      alllist:[],
      id:'',
      limit:18,
      num:'',
      listname:'',

      currentPage: 0,
      total: 0,
      pageSize: 20,
      showli:true
    }
  },
  mounted() {
    this.id = this.$route.query.id
     this.getlist()
  },
  methods:{
    pageChange(pageSize) {
    	const params = {
    		pageSize,
    		currentPage: 10,
    	};
    	// this.$http.post(xxx,{...params}).then(res=>{})
    },
    tourl(url){
      let routeUrl = this.$router.resolve({
                path:url,
              }) // 带参跳转
    
              window.open(routeUrl.href, '_blank');
    },
    shouc(item) {
           console.log(item, "剪辑")
           if (item.is_collect == "true") {
             this.nosc(item.id)
           } else {
             this.oksc(item.id)
           }
         },

         oksc(id) {
           var that = this
           this.$http
             .post(api.user.UserAddCollect, {
               module: 'digitalvenue',
               id: id
             })
             .then((res) => {
               console.log(res)
               if (res.data.code == 0) {
                 this.$message({
                   message: '收藏成功',
                   type: "success",
                 });
                 that.getlist()

               } else {
                 this.$message({
                   message: res.data.msg,
                   type: "error",
                 });
               }
             });
         },
         nosc(id) {
           var that = this
           this.$http
             .post(api.user.UserDelCollect, {
               module: 'digitalvenue',
               id: id
             })
             .then((res) => {
               console.log(res)
               if (res.data.code == 0) {
                 this.$message({
                   message: '取消成功',
                   type: "success",
                 });
                 that.getlist()

               } else {
                 this.$message({
                   message: res.data.msg,
                   type: "error",
                 });
               }
             });
         },

    left(){
      this.NowPage = this.NowPage - 1
       this.getlist()
    },
    next(){
      this.NowPage = this.NowPage + 1
       this.getlist()
    },
    getlist(){
      var that = this
      this.$http
        .post(api.user.CategoryVideoList1, {
               category_id:this.id,
               page:this.NowPage + 1,
               limit:this.limit
        })
        .then((res) => {
           console.log(res)
          if (res.data.code == 0) {
              // that.alllist =
              that.alllist = res.data.data.List
              that.num = res.data.data.Count
              that.pageIndex = res.data.data.pageSize
              that.NowPage = res.data.data.NowPage-1
              that.listname = res.data.data.list.category_name
          } else {
            this.$message({
              message: res.data.res,
              type: "error",
            });
          }
        });
    },
    // 跳转详情
    goTodetail(id){
      this.$router.push({path:'/digitalvenuedetail',query:{ id: id}});
      // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
    },

    // 分页
    pageClick(index){
      console.log(index)
      this.NowPage = index ;
           this.getlist()
    },
  },
}
</script>

<style scoped>
  ul li{
    cursor: pointer;
  }
  .nolist{height: 200px;width: 100%;display: flex;align-items: center;justify-content: center;font-size: 30px;}
.venue{width:100%; height:auto; background: #f5f5f5 url(../assets/img/digitalvenue-bg.jpg) no-repeat center top; background-size:100% auto;}
.bread_crumbs{width:1400px; margin:0 auto; padding-top:190px;}

.digitalvenuelist{width:100%; background-color:#fff; margin-top:55px;}
.digitalvenue-title{width:100%; padding-top:40px; display:flex; flex-flow: row nowrap; align-items: center; justify-content: space-between;}
.digitalvenue-title-left{display:flex;flex-flow: row nowrap; align-items: flex-end;}
.digitalvenue-title-1{font-size:30px; color: #000;}
.digitalvenue-title-2{font-size:16px; color: #666; margin-left:20px;}

.digitalvenue-ul{width: 100%; border-bottom:1px solid #adadad; padding-bottom:40px;}
.digitalvenue-ul ul li{ width:23.5%; margin-right: 2%; overflow:hidden; margin-top:30px; cursor:pointer;}
.digitalvenue-ul ul li:nth-child(4n){margin-right: 0px;}
.digitalvenue-ul-img{width: 100%; height: auto; position: relative;}
.digitalvenue-ul-img img{width: 100%; height: auto; }
.digitalvenue-ul-img .fm{
  width: 100%;
  height: 235px;
  background-size: cover !important;

}

.digitalvenue-ul-img:hover>.prsc {
    z-index: 1;
        opacity: 1;
  }
.digitalvenue-ul-title{width:100%; height: 60px; display: flex; flex-flow:row nowrap; align-items: center; justify-content: space-between;}
.digitalvenue-ul-title p{width:calc(100% - 24px); font-size:18px; color: #000;white-space: nowrap;overflow: hidden;text-overflow: ellipsis;}
.digitalvenue-ul-title img{width:24px;height:24px;}
 .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;

  }

  .digitalvenue-li-img:hover>.prsc {
    z-index: 1;
        opacity: 1;
  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF;
    font-size: 16px;
  }
</style>



