<script>
	import api from "../api/api.js";
	import TablePaging from "../components/Fy";
	import $ from 'jquery';
	export default {
		components: {

			TablePaging,
		},
		name: 'groupexhibition',
		data() {
			return {
				num: 0,
				scroll: 0,
				listtop: {},
				listcenter: [],
				showli: true,
				value: '',
				input: '',
				pr: null,
				city: null,
				sflist: [],
				citylist: [],
				time: '',
				currentPage: 1,
				total: 0,
				pageSize: 9,
				provinceid: '',
				cityid: '',
				list: [],
				isExhibit: 0,
				banner: '',
			}
		},
		mounted() {
			if (localStorage.getItem("userinfo")) {
				this.showli = true
			} else {
				this.showli = false
			}
			// 添加滚动监听事件
			window.addEventListener('scroll', this.handleScroll);
			this.getlisttop()
			this.getlistcenter()
			this.getSF()
		},
		methods: {
			gotozx(id, title) {
				this.$router.push({
					path: '/politicsclassnewsdetail',
					query: {
						id: id,
						title: title
					}
				});
			},
			getlisttop() {
				var that = this
				this.$http
					.post(api.user.RecommendList, {})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							that.listtop = res.data.data

							// that.listcenter = res.data.data.totalNum


						} else if (res.data.code == -100) {
							localStorage.clear();
							that.getlisttop()
							that.showli = false
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			// 分页
			pageChange(value) {
				console.log(value, "hhhh");
				this.currentPage = value;
				this.getlistcenter()
			},
			sho(index) {
				if (index == 1) {
					this.isExhibit = 0
				} else if (index == 2) {
					this.isExhibit = 1
				}
			},
			getindex(item) {
				console.log(item)
				this.pr = item
				this.sflist.forEach((i, x) => {
					if (i.name == item) {
						this.city = ''
						this.citylist = i.city
						this.provinceid = i.id
					}
				});
				console.log(this.provinceid)
			},
			getindex2(item) {
				this.city = item
				this.citylist.forEach((i, x) => {
					if (i.name == item) {
						this.cityid = i.id
					}
				});
				console.log(this.cityid)
			},
			getSF() {
				this.$http
					.post(api.user.sf, {

					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {

							this.sflist = res.data.data
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			getlistcenter() {
				var that = this
				this.$http
					.post(api.user.ExhibitionList, {
						province: this.provinceid,
						displaystatus: this.isExhibit,
						city: this.cityid,
						keyword: this.input,
						start: this.time,
						limit: this.pageSize,
						page: this.currentPage
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							that.listcenter = res.data.data.List
							that.banner = res.data.data.banner
							that.num = res.data.data.count
							that.total = res.data.data.totalNum
							// that.listcenter = res.data.data.totalNum


						} else if (res.data.code == -100) {
							localStorage.clear();
							that.getlistcenter()
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			clearall() {
				this.pr = null,
					this.city = null,
					this.isExhibit = 0
				this.time = ''
				this.input = ''
				this.provinceid = ''
				this.cityid = ''
				this.getlistcenter()
			},
			handleScroll() {
				this.scroll = document.documentElement.scrollTop || document.body.scrollTop
				if (this.scroll > 150) {
					$('.new_header').addClass('active');
				} else {
					$('.new_header').removeClass('active');
				}
			},
			shouc(item) {
				console.log(item, "剪辑")
				if (item.is_collect == "true") {
					this.nosc(item.id)
				} else {
					this.oksc(item.id)
				}
			},
			oksc(id) {
				var that = this
				this.$http
					.post(api.user.UserAddCollect, {
						module: 'exhibition',
						id: id
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							this.$message({
								message: '收藏成功',
								type: "success",
							});
							that.getlistcenter()

						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			nosc(id) {
				var that = this
				this.$http
					.post(api.user.UserDelCollect, {
						module: 'exhibition',
						id: id
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							this.$message({
								message: '取消成功',
								type: "success",
							});
							that.getlistcenter()

						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			// 跳转详情
			goToDetail(id, img) {
				// console.log(link)
				// this.$router.push({
				//   path: `/${link}`
				// });
				this.$router.push({
					path: '/groupexhibitiondetail',
					query: {
						id: id,
						img: img
					}
				});
			},
			tourl(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}) // 带参跳转

				window.open(routeUrl.href, '_blank');
			},
		},
		//销毁滚动事件,否则跳到别的路由还是会出现
		destroyed() {
			window.removeEventListener('scroll', this.handleScroll)
		}
	}
</script>

