		white-space: nowrap;
	}

	.ge-two-info {
		width: 100%;
		height: 35px;
		line-height: 35px;
		font-size: 16px;
		color: #666;
		text-overflow: ellipsis;
		overflow: hidden;
		white-space: nowrap;
	}

	.ge-two-cont {
		width: 100%;
		height: 30px;
	}

	.ge-two-cont p:nth-child(1) {
		width: calc(100% - 75px);
		font-size: 16px;
		color: #666;
	}

	.ge-two-cont-1,
	.ge-two-cont-2 {
		width: 60px;
	}

	.ge-two-cont-1 a {
		display: block;
		width: 100%;
		height: 30px;
		line-height: 30px;
		background-color: #c72c2c;
		font-size: 15px;
		color: #fff;
		text-align: center;
		border-radius: 5px;
	}

	.ge-two-cont-1 a:hover {
		opacity: 0.9;
	}

	.ge-two-cont-2 a {
		display: block;
		width: 100%;
		height: 30px;
		line-height: 30px;
		background-color: #999;
		font-size: 15px;
		color: #fff;
		text-align: center;
		border-radius: 5px;
	}

	.prsc {

		background: rgba(0, 0, 0, 0.5);
		width: 80px;
		height: 32px;
		line-height: 32px;
		position: absolute;
		top: 20px;
		right: 20px;
		color: #FFFFFF;
		font-size: 16px;
		display: flex;
		align-items: center;
		border-radius: 3px;
		justify-content: center;
		cursor: pointer;
		opacity: 0;


	}

	.prsc span {
		font-size: 20px;
		color: #ccc;
		padding-right: 5px;
		display: flex;
		align-items: center;
	}

	.prsc span p {
		color: #FFFFFF !important;
		font-size: 16px;
	}

	.groupexhibition-two ul li:hover>.prsc {
		z-index: 1;
		opacity: 1;
	}

	.groupexhibitionmore-search {
		width: 100%;
		padding-top: 40px;
	}

	.groupexhibitionmore-search>>>.gem-btn.el-button {
		background-color: #ededed;
		color: #000;
		font-size: 16px;
		border: none;
		border-radius: 2px;
	}

	.groupexhibitionmore-search>>>.active.el-button {
		background-color: #c72c2c;
		color: #fff;
	}

	.groupexhibitionmore-search>>>.el-select {
		width: 13%;
	}

	.groupexhibitionmore-search>>>.search-val {
		width: 23%;
	}

	.groupexhibitionmore-search>>>.el-button {
		width: 10%;
	}

	.groupexhibitionmore-search>>>.el-button+.el-button {
		margin-left: 0;
	}

	.showFY {
		width: 100%;
		height: 50px;
		display: flex;
		align-items: center;
		justify-content: center;
		margin-bottom: 30px;
		margin-top: 30px;
	}
</style>



