<template>
  <div class="politicsclass">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <ul>
            <li @click="tourl('/index')"><a>首页</a></li>
            <li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            <li class="active" @click="tourl('/politicsclass')"><a>思政课</a></li>
            <li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            <li @click="tourl('/venue')"><a>革命场馆</a></li>
            <li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>{{title}}</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>思政课</span>
          <span>/</span>
          <span>四史教育</span>
          <span>/</span>
          <span>{{title}}</span>
        </div>
      </div>
    </div>

    <div class="digitalvenuelist">
      <div class="w1400">
        <div class="digitalvenue-title">
          <div class="digitalvenue-title-left">
            <span class="digitalvenue-title-1">{{title}}讲座</span>
            <span class="digitalvenue-title-2">共有<span class="text-c72c2c">{{total}}</span>个视频</span>
          </div>
        </div>
        <div class="digitalvenue-ul">
          <ul class="flex flex-row flex-wrap align-start justify-start">
            <li v-for="(item, index) in centerlist" @click="goTodetail(item.id,item.title)">
              <div class="digitalvenue-ul-img showulung">
                <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'" @click="goTodetail(item.id,item.title)">


                </div>
                </div>
              <div class="digitalvenue-ul-title">
                <p>{{item.title}}</p>
                <img src="../assets/img/dig-play.png">
              </div>
            </li>
          </ul>
       <div class="showFY">
         <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />

       </div>
        </div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  import $ from 'jquery';
  import TablePaging from "../components/Fy";
  export default {
    name: 'politicsclassmore',
    components: {

      TablePaging,
    },
    data() {
      return {
        pageIndex: 0,
        centerlist: [],
        centernum: '',
        id:'',
        currentPage: 1,
        total: 0,
        title:'',
        pageSize: 20,
      }
    },
    mounted() {
      this.id = this.$route.query.id
      this.title = this.$route.query.title
      this.getlist()
    },
    methods: {
      pageChange(value) {
        console.log(value, "hhhh");
        this.currentPage = value;
        this.getlist()
      },
      // 跳转详情
      goTodetail(id,title) {
        // this.$router.push({
        //   path: '/politicsclassmoredetail'
        // });
        this.$router.push({ path: '/politicsclassmoredetail', query:{ id: id,title:title}});
      },
      getlist() {
        var that = this
        this.$http
          .post(api.user.CategoryVideoList, {
            category_id :this.id,
            limit: this.pageSize,
            page: this.currentPage
          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
              that.centerlist = res.data.data.List
              that.centernum = res.data.data.videocount
               that.total = res.data.data.totalNum
            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      // 分页
      pageClick(index) {
        this.pageIndex = index;
      },

      tourl(url) {
        let routeUrl = this.$router.resolve({
          path: url,
        }) // 带参跳转

        window.open(routeUrl.href, '_blank');
      },

    },
  }
</script>

<style scoped>
  .showFY {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
  }
  .politicsclass {
    width: 100%;
    height: auto;
    background: #f5f5f5 url(../assets/img/politicsclass-bg.jpg) no-repeat center top;
    background-size: 100% auto
  }

  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px
  }

  .digitalvenuelist {
    width: 100%;
    background-color: #fff;
    margin-top: 90px;
  }

  .digitalvenue-title {
    width: 100%;
    padding-top: 40px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-title-left {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
  }

  .digitalvenue-title-1 {
    font-size: 30px;
    color: #000;
  }

  .digitalvenue-title-2 {
    font-size: 16px;
    color: #666;
    margin-left: 20px;
  }

  .digitalvenue-ul {
    width: 100%;
    border-bottom: 1px solid #adadad;
    padding-bottom: 40px;
  }

  .digitalvenue-ul ul li {
    width: 23.5%;
    margin-right: 2%;
    overflow: hidden;
    margin-top: 30px;
    cursor: pointer;
  }

  .digitalvenue-ul ul li:nth-child(4n) {
    margin-right: 0px;
  }

  .digitalvenue-ul-img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-img img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-title {
    width: 100%;
    height: 60px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-ul-title p {
    width: calc(100% - 24px);
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .digitalvenue-ul-title img {
    width: 24px;
    height: 24px;
  }
  .showulung {
    position: relative;
  }

  .digitalvenue-ul-img .fm {
    width: 100%;
    height: 220px;
    background-size: cover !important;
    position: relative;
  }
  ul li {
    cursor: pointer;
  }
</style>



