<template>
  <div class="politicsclass">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
        <ul>
          	<li  @click="tourl('/index')"><a>首页</a></li>
          	<li   @click="tourl('/groupexhibition')"><a>数字联展</a></li>
          	<li class="active" @click="tourl('/politicsclass')"><a>思政课</a></li>
          	<li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
          	<li @click="tourl('/venue')"><a>革命场馆</a></li>
          	<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
        </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>

    <div class="zhanwei">&nbsp;</div>
    <div class="w1400 news-detail flex flex-row flex-nowrap align-start justify-between">
      <div class="news-detail-left">
        <h1>{{list.title}}</h1>
        <div class="news-detail-info flex flex-row flex-nowrap align-center justify-start">
          <img src="../assets/img/icon-dy.png" >
          <p class="news-detail-info-text">{{list.writer}}</p>
          <p>|</p>
          <p class="news-detail-info-date">{{list.create_time}}</p>
        </div>
        <div class="news-detail-cont" v-html="list.content">

        </div>
      </div>
      <div class="news-detail-right">
        <div class="news-detail-right-head flex flex-row flex-nowrap align-center justify-between">
          <span>相关资讯</span>
          <img src="../assets/img/detail-more-active.png">
        </div>
        <ul>
          <li v-for="(item, index) in listright" @click="gotoxq(item.id,item.title)">
            <p class="news-detail-right-title"><router-link to="/politicsclassnewsdetail">{{item.title}}</router-link></p>
            <p class="news-detail-right-info">{{item.digest}}</p>
            <div class="mt10 w100 flex flex-row flex-nowrap align-center justify-between">
              <p class="news-detail-right-date">{{item.create_time}}</p>
              <p class="news-detail-right-more"><router-link to="/politicsclassnewsdetail">查看详情</router-link></p>
            </div>
          </li>
        </ul>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
       <ul>
         <li>
           <router-link to="/index">红色足迹</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/groupexhibition">数字联展</router-link>
         </li>
         <li>|</li>
         <li>
           							<router-link to="/groupexhibitionnews">联展资讯</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/digitalvenue">数字联播</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/venue">革命场馆</router-link>
         </li>
         <li>|</li>
         <li>
           <router-link to="/user">个人中心</router-link>
         </li>
       </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
       <div class="new_foot_right">
         <div class="foot_weixin">
           <!-- <router-link to="/index"></router-link> -->
           <div class="showlg"></div>
           <div class="show_div">
                <img src="../assets/img/ewm.png" alt="">
           </div>
         </div>
         <div class="foot_tel">
           <!-- <router-link to="/index"></router-link> -->
                 <div class="showlg"></div>
                <div class="show_div">
                      待更新
                </div>
         </div>
       </div>
      </div>
    </div>
  </div>
</template>

<script>
   import api from "../api/api.js";
import $ from 'jquery';
export default {
  name: 'politicsclassnewsdetail',
  data () {
    return {
        id:'',
        title:'',
        list:{},
        listright:[]
    }
  },
  mounted() {
 this.id = this.$route.query.id
      this.title = this.$route.query.title
      this.getlist()
  },
  methods:{
  tourl(url){
      let routeUrl = this.$router.resolve({
                path:url,
              }) // 带参跳转

              window.open(routeUrl.href, '_blank');
    },
    getlist() {
      var that = this
      this.$http
        .post(api.user.ArticleInfo, {
           article_id: this.id
        })
        .then((res) => {
          console.log(res.data.data)
          if (res.data.code == 0) {
           that.list = res.data.data.info
           that.listright = res.data.data.otherArticle
           that.list.content = that.list.content.replace(/\<img/gi,
             '<img style="max-width:100%;height:auto;margin:auto" ')
           that.list.content = that.list.content.replace(/\<p/gi,
             '<p style="font-size: 18px; color: #000; padding:10px 0; line-height: 30px; text-indent:24px;" ')
          } else if (res.data.code == -100) {
            localStorage.clear();
            that.getlist()
            // this.$router.push("/user");
          } else {
            this.$message({
              message: res.data.res,
              type: "error",
            });
          }
        });
    },
    // 跳转详情
    goToDetail(){
      // this.$router.push({path:'/venuedetail'});
      // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
    },
  gotoxq(id,title){
      // this.$router.push({path:'/venuedetail'});
      // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
      this.id = id
            this.title
            this.getlist()
    },
  },
}
</script>

<style scoped>
  ul li{
    cursor: pointer;
  }
.politicsclass{width:100%;height:auto;}
</style>



