<template>
  <div class="politicsclass"  :style="'background:#f5f5f5 url('+banner+') no-repeat center top;'">
    <div class="new_header header_brotop">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <!--      <ul>
            <li><router-link to="/index">首页</router-link></li>
            <li><router-link to="/groupexhibition">数字联展</router-link></li>
            <li class="active">							<router-link to="/groupexhibitionnews">联展资讯</router-link></li>
            <li><router-link to="/digitalvenue">数字传播</router-link></li>
            <li><router-link to="/venue">革命场馆</router-link></li>
            <li><router-link to="/personalcenter">个人中心</router-link></li>
          </ul> -->
          <ul>
            <li @click="tourl('/index')"><a>首页</a></li>
            <li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            <li class="active" @click="tourl('/politicsclass')"><a>思政课</a></li>
            <li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            <li @click="tourl('/venue')"><a>革命场馆</a></li>
            <li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>思政课</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>思政课</span>
        </div>
      </div>
    </div>

    <div class="politicsclass-one">
      <div class="w1400">
        <div class="digitalvenue-header flex flex-row flex-nowrap align-center justify-between">
          <div class="politicsclass-one-left flex flex-row flex-nowrap align-end justify-start">
            <img src="../assets/img/politicsclass-1.png" alt="">
            <span class="digitalvenue-header-1">相关资讯</span>
            <span class="digitalvenue-header-2">共有<span class="text-c72c2c">{{topnum}}</span>个资讯</span>
          </div>
          <div class="digitalvenue-title-right">
            <router-link to="/groupexhibitionnews">查看更多</router-link>
          </div>
        </div>
        <div class="venue_two">
          <ul>
            <li v-for="(item,index) in toplist" >
              <div class="venue_two_img showulung">
                <!-- <img src="../assets/img/p1.jpg" alt=""> -->

                <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'" @click="gotopdetail(item.id,item.title)">


                </div>
                </div>
              <p class="venue_two_title">{{item.title}}</p>
              <div class="venue_two_more">
                <span>更多</span>
                <span></span>
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="bg-white">
      <div class="w1400">
        <div class="digitalvenue-header flex flex-row flex-nowrap align-center justify-between">
          <div class="politicsclass-one-left flex flex-row flex-nowrap align-end justify-start">
            <img src="../assets/img/video.png" alt="">
            <span class="digitalvenue-header-1">思政课</span>
            <span class="digitalvenue-header-2">共有<span class="text-c72c2c">{{centernum}}</span>个视频</span>
          </div>
          <div class="digitalvenue-title-right">
            <router-link to="/politicsclasslist">查看更多</router-link>
          </div>
        </div>
        <div class="personalcenter_four">
          <ul class="w100 flex flex-row flex-wrap align-start justify-start" v-if="centerlist != undefined && centerlist != null && centerlist.length > 0">
            <li v-for="(item, index) in centerlist" >
              <div class="personalcenter_four_img showulung">
                <!--     <img :src="item.cover"><span>收藏</span> -->
                <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'" @click="goTodetail(item.id,item.title)">


                </div>
                <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
                    <p>收藏</p>
                  </span><span class="el-icon-star-on" v-else>
                    <p>已收藏</p>
                  </span></div>
              </div>
              <div class="personalcenter_four_title">{{item.title}}</div>
              <div class="personalcenter_four_intro">{{item.describe}}</div>
            </li>
          </ul>
		  <div class="w100 flex flex-row flex-wrap align-start justify-start" v-else>
		  	<div class="scdiv">正在加载中！</div>
		  </div>
        </div>

      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  import $ from 'jquery';
  export default {
    name: 'politicsclass',
    data() {
      return {
        scroll: 0,
        centerlist: [],
        centernum: '',
        toplist: [],
        topnum: '',
        showli: true,
				banner:'',
      }
    },
    mounted() {
			if (localStorage.getItem("userinfo")) {
			    this.showli = true
			  }else{
					this.showli = false
			}
      // 添加滚动监听事件
      window.addEventListener('scroll', this.handleScroll);

      $('.venue_two ul li').hover(
        function() {
          $(this).addClass('active');
        },
        function() {
          $(this).removeClass('active');
        }
      );

      $('.personalcenter_four ul li').hover(
        function() {
          $(this).addClass('active');
        },
        function() {
          $(this).removeClass('active');
        }
      );
      this.getlist()
      this.getlisttwo()
    },
    methods: {
      // 跳转详情
      // goTodetail(id) {
      //   // this.$router.push({
      //   //   path: '/digitalvenuedetail',
      //   //   query: {
      //   //     id: id
      //   //   }
      //   // });
      //   // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
      // },
      gotopdetail(id,title){
           this.$router.push({ path: '/politicsclassnewsdetail', query:{ id: id,title:title}});
      },
      getlist() {
        var that = this
        this.$http
          .post(api.user.EducationVideoList, {

          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
              that.centerlist = res.data.data.list
              that.centernum = res.data.data.videocount
									         that.banner = res.data.data.banner
              // that.list1 = res.data.data.categoryList
              // that.datas = res.data.data.data
              // that.videolist = res.data.data.TOPSmall
              // that.videoone = res.data.data.TOPBig
              // that.num = res.data.data.Allcount
            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
									that.showli = false
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      getlisttwo() {
        var that = this
        this.$http
          .post(api.user.EducationRecommendList, {

          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
               that.toplist = res.data.data.recommend
               that.topnum = res.data.data.count
							 
            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
 shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },
      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'education',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'education',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      tourl(url) {
        let routeUrl = this.$router.resolve({
          path: url,
        }) // 带参跳转

        window.open(routeUrl.href, '_blank');
      },
      handleScroll() {
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        if (this.scroll > 150) {
          $('.new_header').addClass('active');
        } else {
          $('.new_header').removeClass('active');
        }
      },

      // 跳转详情
      goTodetail(id,title) {
        // this.$router.push({
        //   path: '/politicsclassmoredetail'
        // });
        this.$router.push({ path: '/politicsclassmore', query:{ id: id,title:title}});
      },

      goToMore() {
         this.$router.push({path:'/politicsclassmore'});
      }

    },
    //销毁滚动事件,否则跳到别的路由还是会出现
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>

<style scoped>
  ul li {
    cursor: pointer;
  }

  .politicsclass {
    width: 100%;
    height: auto;
    background: #f5f5f5 url(../assets/img/politicsclass-bg.jpg) no-repeat center top;
    background-size: 100% auto
  }

  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px
  }

  .politicsclass-one {
    width: 100%;
    margin-top: 120px;
    background-color: #f5f5f5
  }

  .digitalvenue-header {
    width: 100%;
    height: 45px;
    padding: 50px 0 20px;
    border-bottom: 1px solid #cacaca
  }

  .politicsclass-one-left {
    width: 50%
  }

  .politicsclass-one-left img {
    width: 57px;
    height: 45px
  }

  .digitalvenue-header-1 {
    font-size: 36px;
    color: #000;
    margin-left: 15px
  }

  .digitalvenue-header-2 {
    font-size: 16px;
    color: #666;
    margin-left: 20px
  }

  .digitalvenue-title-right {
    background: url(../assets/img/dig-more.png) no-repeat right center;
    background-size: 14px 19px;
    padding-right: 30px;
    margin-right: 10px;
    font-size: 18px
  }

  .digitalvenue-title-right a {
    color: #c71925
  }

  .digitalvenue-title-right a:hover {
    color: #c71925;
    opacity: .9
  }

  .venue_two {
    width: 1400px;
    height: auto;
    margin: 0 auto;
    padding: 50px 0
  }

  .venue_two ul {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: flex-start
  }

  .venue_two ul li {
    width: 23.5%;
    height: auto;
    background-color: #fff;
    margin-right: 2%;
    cursor: pointer;
    overflow: hidden
  }

  .venue_two ul li:nth-child(4n) {
    margin-right: 0
  }

  .venue_two ul li .venue_two_img {
    width: 100%;
    height: 184px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center
  }

  .venue_two ul li .venue_two_img img {
    width: 100%;
    height: auto;
    position: relative;
    transition: all 1s
  }

  .venue_two ul li .venue_two_title {
    margin: 0 30px;
    width: calc(100% - 60px);
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #e6e6e6;
    font-size: 18px;
    color: #000
  }

  .venue_two ul li .venue_two_more {
    margin: 0 30px;
    width: calc(100% - 60px);
    height: 50px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between
  }

  .venue_two ul li .venue_two_more span {
    display: block
  }

  .venue_two ul li .venue_two_more span:first-child {
    color: rgba(0, 0, 0, .5);
    font-size: 16px
  }

  .venue_two ul li .venue_two_more span:last-child {
    width: 19px;
    height: 6px;
    background: url(../assets/img/more.png) no-repeat center center;
    background-size: 100% auto
  }

  .venue_two ul li.active {
    background-color: #b5001f
  }

  .venue_two ul li.active .venue_two_img img {
    -webkit-transform: scale(1.05, 1.05)
  }

  .venue_two ul li.active .venue_two_title {
    color: #fff
  }

  .venue_two ul li.active .venue_two_more span:first-child {
    color: rgba(255, 255, 255, .5)
  }

  .venue_two ul li.active .venue_two_more span:last-child {
    background: url(../assets/img/more-active.png) no-repeat center center
  }

  .personalcenter_four {
    width: 100%;
    padding-bottom: 100px
  }

  .personalcenter_four ul li {
    width: 32%;
    margin-top: 30px;
    margin-right: 2%;
    cursor: pointer;
  }

  .personalcenter_four ul li:nth-child(3n) {
    margin-right: 0
  }

  .personalcenter_four_img {
    width: 100%;
    height: 280px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative
  }

  .personalcenter_four_img img {
    width: 100%;
    min-height: 280px
  }

  /* .personalcenter_four ul li .personalcenter_four_img span {
    position: absolute;
    top: 20px;
    right: 20px;
    display: inline-block;
    opacity: 0;
    background: rgba(0, 0, 0, .5) url(../assets/img/star.png) no-repeat 5px center;
    color: #fff;
    width: 40px;
    height: 32px;
    line-height: 32px;
    overflow: hidden;
    font-size: 16px;
    padding-left: 25px;
    border-radius: 3px;
    cursor: pointer
  } */

/*  .personalcenter_four ul li.active .personalcenter_four_img span {
    opacity: 1
  } */

  .personalcenter_four_title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
  }

  .personalcenter_four_intro {
    font-size: 16px;
    color: #666;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden
  }

  .showulung {
    position: relative;
  }

  .personalcenter_four_img .fm {
    width: 100%;
    height: 280px;
    background-size: cover !important;
    position: relative;
  }

  .venue_two_img .fm {
    width: 100%;
    height: 184px;
    background-size: cover !important;
    position: relative;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;


  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF !important;
    font-size: 16px;
  }
  .personalcenter_four_img:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }
</style>



