<template>
  <div class="venue" id="venueall" :style="'background:#f5f5f5 url('+banner+') no-repeat center top;'">
    <div class="new_header header_brotop">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
       <!--   <ul>
            <li>
              <router-link to="/index">首页</router-link>
            </li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>
              <router-link to="/digitalvenue">数字传播</router-link>
            </li>
            <li class="active">
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>
              <router-link to="/personalcenter">个人中心</router-link>
            </li>
          </ul> -->
          <ul>
            	<li  @click="tourl('/index')"><a>首页</a></li>
            	<li  @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            	<li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
            	<li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            	<li class="active" @click="tourl('/venue')"><a>革命场馆</a></li>
            	<li  @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>革命场馆</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>革命场馆</span>
        </div>
      </div>
    </div>

    <div class="venue_one">
      <div class="venue_search flex">
        <input type="text" placeholder="输入关键字搜索" v-model="search">
        <div class="btn-search"><img src="../assets/img/search.png" @click="getall"></div>
      </div>
      <div class="search_ul flex flex-row flex-nowrap justify-start">
        <div class="search_left">省份</div>
        <ul class="search_li flex flex-row flex-wrap align-center justify-start">
          <li :class="provinceIndex==index?'active':''" v-for="(item,index) in provinceData" :key="index" @click="province(item,index)">{{item.name}}</li>
        </ul>
      </div>
      <div class="search_ul flex flex-row flex-nowrap justify-start" v-if="cityshow">
        <div class="city_left">城市</div>
        <ul class="search_li flex flex-row flex-wrap align-center justify-start">
          <li :class="cityIndex==index?'active':''" v-for="(item,index) in cityData" :key="index" @click="city(item,index)">{{item.name}}</li>
        </ul>
      </div>
    </div>
    <div class="venue_two">
      <ul v-if="list.length>0">
        <li v-for="(item,index) in list">
          <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
              <p>收藏</p>
            </span><span class="el-icon-star-on" v-else>
              <p>已收藏</p>
            </span></div>
          <div class="venue_two_img" @click="goToDetail(item.id)" :style="'background:url('+item.cover+') center no-repeat;'">
            <!-- <div class="personalcenter_four_img"><img src="../assets/img/p2.jpg"><span>收藏</span></div> -->
            <!-- <img :src="item.cover" alt=""> -->
          </div>
          <p class="venue_two_title" @click="goToDetail(item.id)">{{item.building_name}}</p>
          <div class="venue_two_more" @click="goToDetail(item.id)">
            <span>更多</span>
            <span></span>
          </div>
        </li>
      </ul>
      <div class="nomore" v-else>
        正在加载中！
      </div>
    </div>
    <div class="showFY">
      <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />

    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
      <div class="new_foot_right">
        <div class="foot_weixin">
          <!-- <router-link to="/index"></router-link> -->
          <div class="showlg"></div>
          <div class="show_div">
               <img src="../assets/img/ewm.png" alt="">
          </div>
        </div>
        <div class="foot_tel">
          <!-- <router-link to="/index"></router-link> -->
                <div class="showlg"></div>
               <div class="show_div">
                     待更新
               </div>
        </div>
      </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  import $ from 'jquery';
  import TablePaging from "../components/Fy";
  export default {
    name: 'venue',
    components: {

      TablePaging,
    },
    data() {
      return {
        scroll: 0,
        provinceData: [],
        provinceIndex: 0,
        cityData: [],
        cityIndex: 0,
        provinceid: '',
        cityid: '',
        search: '',
        loading: null,
        list: [],
        cityshow: true,

        currentPage: 1,
        total: 0,
        pageSize: 20,
        showli: false,
				banner:'',
      }
    },
    mounted() {
      if (localStorage.getItem("userinfo")) {
        this.showli = true
      }else{
				this.showli = false
		}
			
      // 添加滚动监听事件
      window.addEventListener('scroll', this.handleScroll);


      $('.venue_two ul li').hover(
        function() {
          $(this).addClass('active');
        },
        function() {
          $(this).removeClass('active');
        }
      );

      this.getList()

    },
    methods: {
      tourl(url){
        let routeUrl = this.$router.resolve({
                  path:url,
                }) // 带参跳转

                window.open(routeUrl.href, '_blank');
      },
      pageChange(value) {
        console.log(value, "hhhh");
        this.currentPage = value;
        this.getList()
      },
      getall() {
        var that = this
        this.$http
          .post(api.user.StadiumList, {
            province: this.provinceid,
            city: this.cityid,
            keyword: this.search,
            limit: this.pageSize,
            page: this.currentPage
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              that.list = res.data.data.List
              that.total = res.data.data.totalNum
			that.banner = res.data.data.banner		
              // that.pageSize = res.data.data.pageSize
              // that.currentPage = that.currentPage + 1

            }else if(res.data.code == -100){
              localStorage.clear();
              that.getall()
			  that.showli = false
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      getList() {

        this.$http
          .post(api.user.sf, {

          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              this.provinceData = res.data.data
              this.provinceData.unshift({
                id: '',
                name: "全部",
              });
              this.cityData.push({
                id: '',
                name: "全部",
              })
              this.getall()
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      handleScroll() {
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        if (this.scroll > 150) {
          $('.new_header').addClass('active');
        } else {
          $('.new_header').removeClass('active');
        }
      },

      // 省份选择
      province(item, index) {
        this.cityData = [];
        this.provinceIndex = index;
        this.provinceid = item.id
        console.log(this.provinceid)
        this.currentPage = 1
        this.provinceData.forEach((i, x) => {
          if (i.name == item.name) {
            if ((item.name || i.name) == "全部") {
              this.cityData.push({
                name: '全部',
                id: '',

              })
              this.cityshow = true
              this.provinceid = '',
                this.cityid = '',
                this.cityIndex = 0,
                this.getall()
              return false
            }
            if (item.name == "北京") {
              this.cityData.push({
                name: '全部',
                id: '',

              })
              this.cityshow = false
              this.provinceid = '110000',
                this.cityid = '',
                this.cityIndex = 0,
                this.getall()
              return false
            }
            if (item.name == "天津") {
              this.cityData.push({
                name: '全部',
                id: '',

              })
              this.cityshow = false
              this.provinceid = '120000',
                this.cityid = '',
                this.cityIndex = 0,
                this.getall()
              return false
            }
            if (item.name == "上海") {
              this.cityData.push({
                name: '全部',
                id: '',

              })
              this.cityshow = false
              this.provinceid = '310000',
                this.cityid = '',
                this.cityIndex = 0,
                this.getall()
              return false
            }
            if (item.name == "重庆") {
              this.cityData.push({
                name: '全部',
                id: '',

              })
              this.cityshow = false
              this.provinceid = '500000',
                this.cityid = '',
                this.cityIndex = 0,
                this.getall()
              return false
            }
            this.cityshow = true
            this.cityData = i.city
            if (this.cityData.length < 1) {
              this.cityData.push({
                name: '全部',
                id: '',

              })
            } else if (this.cityData[0].name != '全部') {

              this.cityData.unshift({
                name: '全部',
                id: '',

              })

            }
            this.cityIndex = 0,
              this.getall()
          }
        });
      },
      shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },

      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'venue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getall()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'venue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getall()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      // 城市选择
      city(item, index) {

        this.cityIndex = index;
        this.cityid = item.id
        this.currentPage = 1
        console.log(this.cityid)
        this.getall()
      },
      // 跳转详情
      goToDetail(id) {
        // this.$router.push({
        //   path: '/venuedetail'
        // });
        this.$router.push({
          path: '/venuedetail',
          query: {
            id: id
          }
        });
      }

    },
    //销毁滚动事件,否则跳到别的路由还是会出现
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>

<style scoped>
  .venue {
    width: 100%;
    height: auto;
    background: #f5f5f5 url(../assets/img/venue-bg.jpg) no-repeat center top;
    background-size: 100% auto;
  }
  ul li{
    cursor: pointer;
  }
  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px;
  }

  .venue_one {
    width: 1350px;
    height: auto;
    margin: 40px auto 0;
    padding: 25px;
    background-color: #fff;
  }

  .venue_search {
    width: calc(100% - 25px);
    padding-right: 25px;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
    background-color: #f5f5f5;
    border-radius: 5px;
  }

  .venue_search input {
    width: calc(100% - 50px);
    padding: 25px;
    font-size: 18px;
    color: rgba(0, 0, 0, .5);
    border: 0;
    background-color: #f5f5f5;
  }

  .btn-search {
    width: 24px;
    height: auto;
  }

  .btn-search img {
    width: 24px;
    height: 25px;
    overflow: hidden;
  }

  .search_ul {
    width: 100%;
    margin-top: 28px;
  }

  .search_left {
    width: 65px;
    height: 36px;
    line-height: 36px;
    padding-left: 60px;
    font-size: 18px;
    background: url(../assets/img/map.png) no-repeat 18px center;
    background-size: 26px 25px;
  }

  .search_li {
    width: calc(100% - 125px);
    height: auto;
  }

  .search_li li {
    font-size: 18px;
    color: #000;
    padding: 0px 25px;
    height: 36px;
    line-height: 36px;
    cursor: pointer;
    margin-bottom: 20px;
  }

  .search_li li.active {
    background-color: #b5001f;
    color: #fff;
    border-radius: 3px;
  }

  .city_left {
    width: 65px;
    height: 36px;
    line-height: 36px;
    padding-left: 60px;
    font-size: 18px;
    background: url(../assets/img/bowuguan.png) no-repeat 18px center;
    background-size: 26px 25px;
  }

  .showFY {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
  }

  .nomore {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
    height: 200px;
    font-size: 28px;
  }

  .venue_two {
    width: 1400px;
    height: auto;
    margin: 0 auto;
    padding-bottom: 70px;
  }

  .venue_two ul {
    display: flex;
    flex-flow: row wrap;
    align-items: flex-start;
    justify-content: flex-start;
  }

  .venue_two ul li {
    width: 23.5%;
    height: auto;
    background-color: #fff;
    margin-top: 30px;
    margin-right: 2%;
    cursor: pointer;
    overflow: hidden;
    position: relative;
  }

  .venue_two ul li:nth-child(4n) {
    margin-right: 0;
  }

  .venue_two ul li .venue_two_img {
    width: 100%;
    height: 184px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
    background-size: cover !important;
  }

  .venue_two ul li .venue_two_img img {
    width: 100%;
    /* height: auto; */
    height: 100%;
    position: relative;
    transition: all 1s;
  }

  .venue_two ul li .venue_two_title {
    margin: 0 30px;
    width: calc(100% - 60px);
    height: 50px;
    line-height: 50px;
    border-bottom: 1px solid #e6e6e6;
    font-size: 18px;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .venue_two ul li .venue_two_more {
    margin: 0 30px;
    width: calc(100% - 60px);
    height: 50px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;

  }

  .venue_two ul li:hover>.prsc {
    z-index: 1;
        opacity: 1;
  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF;
    font-size: 16px;
  }

  .venue_two ul li.active .prsc span {
    z-index: 1;
  }

  .venue_two ul li .venue_two_more span {
    display: block;
  }

  .venue_two ul li .venue_two_more span:first-child {
    color: rgba(0, 0, 0, .5);
    font-size: 16px;
  }

  .venue_two ul li .venue_two_more span:last-child {
    width: 19px;
    height: 6px;
    background: url(../assets/img/more.png) no-repeat center center;
    background-size: 100% auto;
  }

  .venue_two ul li.active {
    background-color: #b5001f;
  }

  .venue_two ul li.active .venue_two_img img {
    -webkit-transform: scale(1.05, 1.05);
  }

  .venue_two ul li.active .venue_two_title {
    color: #fff;
  }

  .venue_two ul li.active .venue_two_more span:first-child {
    color: rgba(255, 255, 255, .5);
  }

  .venue_two ul li.active .venue_two_more span:last-child {
    background: url(../assets/img/more-active.png) no-repeat center center;
  }

  /* .personalcenter_four_img{width:100%;height:280px;overflow:hidden;display:flex;align-items:center;justify-content:center;position:relative;position: absolute;right: 10px;}
  .personalcenter_four_img img{width:100%;min-height:280px}
  .personalcenter_four ul li .personalcenter_four_img span{position:absolute;top:20px;right:20px;display:inline-block;opacity:0;background:rgba(0,0,0,.5) url(../assets/img/star.png) no-repeat 5px center;color:#fff;width:40px;height:32px;line-height:32px;overflow:hidden;font-size:16px;padding-left:25px;border-radius:3px;cursor:pointer}
  .personalcenter_four ul li.active .personalcenter_four_img span{opacity:1} */
</style>



