          	<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
        </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>思政课</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span><router-link to="/index">首页</router-link></span>
          <span>/</span>
          <span>思政课</span>
          <span>/</span>
          <span>新闻资讯</span>
        </div>
      </div>
    </div>

    <div class="politicsclasslist-search">
      <div class="w1400 flex flex-row flex-nowrap align-center justify-between">
        <el-select v-model="value" placeholder="省份: 全部">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="value" placeholder="城市: 全部">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-select v-model="value" placeholder="时间: 全部">
          <el-option
            v-for="item in options"
            :key="item.value"
            :label="item.label"
            :value="item.value">
          </el-option>
        </el-select>
        <el-input class="search-val" v-model="input" placeholder="名称"></el-input>
        <el-button>搜索</el-button>
      </div>
    </div>
    <div class="politicsclassnews">
      <ul>
        <li class="flex flex-row flex-nowrap align-start justify-between" v-for="(item,index) in 5" @click="goToDetail()">
          <div class="pcn-dates">
            <p>0{{index+1}}</p>
            <p>&nbsp;</p>
            <p>2021.12</p>
          </div>
          <div class="pcn-img"><img src="../assets/img/v3.jpg"><span class="span1">&nbsp;</span><span class="span2">&nbsp;</span></div>
          <div class="pcn-cont flex flex-direction align-start justify-between">
            <p class="pcn-cont-title">习近平总书记瞻仰的主要革命圣地、红色旧址、革命历史红色资源是我们党艰辛而辉煌奋斗历程的见证</p>
            <p class="pcn-cont-info">习近平总书记在主持中共中央政治局第三十一次集体学习时强调，红色资源是我们党艰辛而辉煌奋斗历程的见证，是最宝贵的精神财富...</p>
            <div class="w100 flex flex-row flex-nowrap align-center justify-between">
              <span class="pcn-cont-address">安徽</span>
              <p class="pcn-cont-more">&nbsp;</p>
            </div>
          </div>
        </li>
      </ul>
      <div class="page flex flex-row flex-nowrap align-center justify-center">
        <div class="page-prev" v-if="pageIndex > 0"><img src="../assets/img/prev.png" ></div>
        <p :class="pageIndex == index?'active':''" v-for="(item, index) in 3" :key="index" @click="pageClick(index)">{{index+1}}</p>
        <div class="page-next" v-if="pageIndex < 2"><img src="../assets/img/next.png" ></div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
