          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
     <div class="new_foot_right">
       <div class="foot_weixin">
         <!-- <router-link to="/index"></router-link> -->
         <div class="showlg"></div>
         <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
         </div>
       </div>
       <div class="foot_tel">
         <!-- <router-link to="/index"></router-link> -->
               <div class="showlg"></div>
            <div class="show_div">
                  待更新
            </div>
       </div>
     </div>
      </div>
    </div>
  </div>
</template>

<script>
import $ from 'jquery';
export default {
  name: 'politicsclass',
  data () {
    return {
      scroll: 0,
      options: [{
        value: '选项1',
        label: '黄金糕'
      }, {
        value: '选项2',
        label: '双皮奶'
      }, {
        value: '选项3',
        label: '蚵仔煎'
      }, {
        value: '选项4',
        label: '龙须面'
      }, {
        value: '选项5',
        label: '北京烤鸭'
      }],
      value: '',
      input: '',
      pageIndex: 0,
    }
  },
  mounted() {
    // 添加滚动监听事件
    window.addEventListener('scroll',this.handleScroll);

    $('.politicsclassnews ul li').hover(
    	function() {
    		$(this).addClass('active');
    	},
    	function() {
    		$(this).removeClass('active');
    	}
    );
  },
  methods:{
  tourl(url){
      let routeUrl = this.$router.resolve({
                path:url,
              }) // 带参跳转

              window.open(routeUrl.href, '_blank');
    },
    handleScroll(){
      this.scroll = document.documentElement.scrollTop||document.body.scrollTop
      if(this.scroll > 150){
        $('.new_header').addClass('active');
      }else{
        $('.new_header').removeClass('active');
      }
    },

    // 跳转详情
    goToDetail(){
      this.$router.push({path:'/politicsclassnewsdetail'});
      // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
    },

  },
  //销毁滚动事件,否则跳到别的路由还是会出现
  destroyed(){
    window.removeEventListener('scroll',this.handleScroll)
  }
}
</script>

<style scoped>
  ul li{
    cursor: pointer;
  }
.politicsclass{width:100%;height:auto;background:#f5f5f5 url(../assets/img/politicsclass-bg.jpg) no-repeat center top;background-size:100% 428px}
.bread_crumbs{width:1400px;margin:0 auto;padding-top:190px}

.politicsclasslist-search{width:100%;background-color: #f5f5f5;margin-top:90px; padding-top:40px;}
.politicsclasslist-search >>> .el-select{width: 15%;}
.politicsclasslist-search >>> .search-val{width: 40%;}
.politicsclasslist-search >>> .el-button{width: 10%;}

.politicsclassnews{width: 1400px; padding-top:40px; margin:0 auto; padding-bottom:50px;}
.politicsclassnews ul{ padding-bottom: 50px;}
.politicsclassnews ul li{width: 100%; height: 280px; margin-bottom: 20px;overflow:hidden;cursor:pointer;}
.pcn-dates{width: 8%; height:100%; display: flex; flex-direction: column; align-items: flex-start; justify-content: center;color: #b5001f; padding-left: 5%;}
.pcn-dates p:nth-child(1){font-size:48px;}
.pcn-dates p:nth-child(2){width:15px; height: 1px; background-color: #b5001f; margin: 5px 0 10px;}
.pcn-dates p:nth-child(3){font-size: 18px;}
.pcn-img{width: 31%; height: 100%; position: relative; display: flex; justify-content: center; align-items: center;background-color: #fff;}
.pcn-img img{width: 100%;}
.pcn-img .span1{ display: inline-block; width:0; height: 120px; border-width:10px 0 10px 10px; border-style: solid; border-color: #f5f5f5 transparent transparent #f5f5f5; position: absolute; top: 0; left: 0;z-index: 2;}
.pcn-img .span2{ display: inline-block; width:0; height: 130px; border-width:10px 10px 0 10px; border-style: solid; border-color: transparent transparent transparent #f5f5f5; position: absolute; bottom: 0; left: 0; z-index: 2;}
.pcn-cont{width:calc(56% - 120px); height: 180px; padding: 50px 60px;background-color: #fff;}
.pcn-cont-title{width:100%;font-size:24px;color:#000; text-overflow: ellipsis; overflow:hidden; white-space: nowrap;}
.pcn-cont-info{width:100%;font-size:16px; color:#666; display:-webkit-box;-webkit-box-orient:vertical;-webkit-line-clamp:2;overflow:hidden}
.pcn-cont-address{font-size:18px;color:rgba(0,0,0,0.7); background:url(../assets/img/pol-address.png) no-repeat left center; background-size:18px 18px; padding-left:28px;}
.pcn-cont-more{width:28px; height: 7px; background:url(../assets/img/pol-more.png) no-repeat center center; background-size:cover;}

.politicsclassnews ul li.active .pcn-dates{background-color: #b5001f;color: #fff;}
.politicsclassnews ul li.active .pcn-dates p:nth-child(2){ background-color: #fff;}
.politicsclassnews ul li.active .pcn-img .span1{ border-color: #b5001f transparent transparent #b5001f;}
.politicsclassnews ul li.active .pcn-img .span2{ border-color: transparent transparent transparent #b5001f;}
.politicsclassnews ul li.active .pcn-cont-more{ background:url(../assets/img/pol-more-active.png) no-repeat center center;}

</style>



