<template>
  <div class="digitalvenuedetail">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <ul>
            <li @click="tourl('/index')"><a>首页</a></li>
            <li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            <li class="active" @click="tourl('/politicsclass')"><a>思政课</a></li>
            <li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            <li @click="tourl('/venue')"><a>革命场馆</a></li>
            <li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>

    <div class="detail-bread">
      <div class="w1400">
        <div class="detail-bread-ul">
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>思政课</span>
          <span>/</span>
          <span>{{title}}</span>
          <span>/</span>
          <span>详情</span>
        </div>

        <div class="w1400 flex flex-row flex-nowrap align-start justify-between">
          <div class="politicsclasslistdetail-left">
            <div class="detail-title">
              <p class="detail-title-1">{{title}}</p>
              <p class="detail-title-2">{{centerlist.create_time}}</p>
            </div>
            <div class="detail-video">
              <video controls="controls" :src="centerlist.video" controlslist="nodownload noremoteplayback"
                :disablePictureInPicture="true"></video>
            </div>
          </div>
          <div class="politicsclasslistdetail-right">
            <p class="pcld-right-title">更多课程</p>
            <ul>
              <el-scrollbar style="height:100%">
                <li v-for="(item, index) in centerlist.more" @click="godet(item.id,item.title)">
                  <div class="pcld-right-img"><img :src="item.cover"><span>&nbsp;</span></div>
                  <div class="pcld-right-cont">
                    <p class="pcld-right-cont-name">{{item.title}}</p>
                    <p class="pcld-right-cont-time">{{item.create_time}}</p>
                  </div>
                </li>
              </el-scrollbar>
            </ul>
          </div>
        </div>

      </div>
    </div>
    <div class="detail-tuijian">
      <div class="w1400">
        <div class="digitalvenue-title">
          <div class="digitalvenue-title-left">
            <span class="digitalvenue-title-1">推荐视频</span>
          </div>
          <div class="digitalvenue-title-right">
            <router-link to="/politicsclasslist">查看更多</router-link>
          </div>
        </div>
        <div class="digitalvenue-ul">
          <ul class="flex flex-row flex-wrap align-start justify-start">
            <li v-for="(item, indexs) in centerlist.recommend" @click="godet(item.id,item.title)">
              <div class="digitalvenue-ul-img"><img :src="item.cover"></div>
              <div class="digitalvenue-ul-title">
                <p>{{item.title}}</p>
                <img src="../assets/img/dig-play.png">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <div class="dingzhibox flex align-center justify-center" @click="isShip = true">定制线下思政课</div>
    <div class="dingzhi-bg" v-if="isShip == true">
      <div class="dingzhi">
        <div class="bg-white">
          <h1>您的联系方式</h1>

          <p class="dingzhibox-tips">请您正确填写表单中的信息，其中带*为必填项，我们将在收到您提交的信息后，及时与您取得联系。</p>
          <el-form ref="shipForm" :model="shipForm" label-width="120px">
            <el-row>
              <el-col :span="24">
                <el-form-item label="姓名" prop="name" :rules="[{required:true,message:'姓名不能为空'}]">
                  <el-input v-model="shipForm.name" placeholder="请填写您的真实姓名"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="联系方式" prop="phone" :rules="[{required:true,message:'联系方式不能为空'}]">
                  <el-input v-model="shipForm.phone" placeholder="请填写电话号码"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="邮箱" prop="email" :rules="[{required:true,message:'邮箱不能为空'}]">
                  <el-input v-model="shipForm.email" placeholder="请填写您的邮箱"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="公司名称" prop="company">
                  <el-input v-model="shipForm.company" placeholder="请填写您的公司名称"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="地址" prop="dvyFlowId">
                  <el-select v-model="shipForm.province" @change='getindex(shipForm.province)' placeholder="省份">
                    <el-option v-for="(item,index) in sflist" :key="index" :label="item.name" :value="item.name"></el-option>
                  </el-select>
                  <el-select v-model="shipForm.city" placeholder="城市" @change='getindex2(shipForm.city)'>
                    <el-option v-for="(item,index) in citylist" :key="index" :label="item.name" :value="item.name"></el-option>
                  </el-select>
                  <el-select v-model="shipForm.area" placeholder="街道" @change='getindex3(shipForm.area)'>
                    <el-option v-for="(item,index) in arealist" :key="index" :label="item.name" :value="item.name"></el-option>
                  </el-select>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="" prop="address">
                  <el-input v-model="shipForm.address" placeholder="请填写详细地址"></el-input>
                </el-form-item>
              </el-col>
              <el-col :span="24">
                <el-form-item label="备注" prop="remark">
                  <el-input type="textarea" :autosize="{ minRows: 4, maxRows: 6}" v-model="shipForm.remark" maxlength="200"
                    show-word-limit placeholder="请填写您的备注"></el-input>
                </el-form-item>
              </el-col>
            </el-row>
            <el-form-item>
              <el-button type="danger" @click="saveLog">提交</el-button>
            </el-form-item>
          </el-form>
          <div class="dingzhi-close" @click="isShip = false"><i class="el-icon-close text-c72c2c"></i></div>
        </div>
      </div>
    </div>
    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import api from "../api/api.js";
  export default {
    name: 'digitalvenuedetail',
    data() {
      return {
        isDingZhi: false,
        isShip: false,
        shipForm: {
          name: '',
          phone: '',
          email: '',
          province: '',
          city: '',
          area: '',
          remark: '',
          company: '',
        },

        id: '',
        title: '',
        centerlist: null,
        sflist: [],
        citylist: [],
        arealist: []
      }
    },
    mounted() {
      this.id = this.$route.query.id
      this.title = this.$route.query.title
      this.getlist()
      this.getssq()
    },
    methods: {
      tourl(url) {
        let routeUrl = this.$router.resolve({
          path: url,
        }) // 带参跳转

        window.open(routeUrl.href, '_blank');
      },
      gotoalllist() {
        this.$router.push({
          path: '/politicsclasslist'
        });
      },
      saveLog() {
        this.$refs['shipForm'].validate((valid) => {
          if (valid) {

            var that = this
            var data = this.shipForm
              var myreg = /^([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+@([a-zA-Z0-9]+[_|\_|\.]?)*[a-zA-Z0-9]+\.[a-zA-Z]{2,3}$/;
           if(!/^1[3456789]\d{9}$/.test(data.phone)){
             this.$message({
               message: '请输入正确的手机号',
               type: "error",
             });
             return false
           }else if(!myreg.test(data.email)){
             this.$message({
               message: '请输入正确邮箱',
               type: "error",
             });
             return false
           }
            this.$http
              .post(api.user.SubInformation, data)
              .then((res) => {
                console.log(res.data.data)
                if (res.data.code == 0) {
                  this.$message({
                    message: res.data.msg,
                    type: "success",
                  });
                  that.isShip = false

                } else if (res.data.code == -100) {
                  localStorage.clear();
                  // this.$router.push("/user");
                } else {
                  this.$message({
                    message: res.data.res,
                    type: "error",
                  });
                }
              });
          } else {
            return false
          }
        })
      },
      godet(id, title) {
        // this.$router.push({ path: '/politicsclassmoredetail', query:{ id: id,title:title}});
        this.id = id
        this.title = title
        this.getlist()
      },
      getindex(item) {
        console.log(item)
        this.shipForm.province = item
        this.sflist.forEach((i, x) => {
          if (i.name == item) {
            this.shipForm.city = ''
            this.shipForm.area = ''
            this.citylist = i.City
          }
        });
      },
      getindex2(item) {
        console.log(item)
        this.shipForm.city = item
        this.citylist.forEach((i, x) => {
          if (i.name == item) {
            this.shipForm.area = ''
            this.arealist = i.area
          }
        });
      },
      getindex3(item) {
        console.log(item)
        this.shipForm.area = item
      },
      getssq() {
        var that = this
        this.$http
          .post(api.user.location, {

          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
              that.sflist = res.data.data.Province

            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      getlist() {
        var that = this
        this.$http
          .post(api.user.EducationVideoInfo, {
            id: this.id
          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
              that.centerlist = res.data.data

            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
    },
  }
</script>

<style scoped>
  ul li {
    cursor: pointer;
  }

  .dingzhi {
    width: 980px;
    height: auto;
    border: 10px solid rgba(199, 44, 44, 0.5);
    border-radius: 10px;
    overflow: hidden;
    position: fixed;
    top: 10vh;
    left: calc(50% - 490px);
  }

  .dingzhi .bg-white {
    width: 780px;
    border: 2px solid #c72c2c;
    padding: 40px 98px;
  }

  .dingzhi h1 {
    font-size: 36px;
    color: #000;
    text-align: center;
    margin: 0;
  }

  .dingzhi .dingzhibox-tips {
    font-size: 16px;
    color: #999;
    text-align: center;
    margin-top: 20px;
  }

  .dingzhi>>>.el-form {
    margin-top: 30px;
  }

  .dingzhi>>>.el-form-item__label {
    color: #252631;
    font-size: 18px;
  }

  .dingzhi>>>.el-button--danger {
    background: #c72c2c;
    font-size: 20px;
    padding: 15px 50px;
  }

  .dingzhi-close {
    position: absolute;
    top: 20px;
    right: 20px;
  }

  .dingzhi-close i {
    font-size: 30px;
    font-weight: bold;
    cursor: pointer;
  }

  .dingzhi>>>.el-input__inner,
  .dingzhi>>>.el-textarea__inner {
    border: 1px solid #c72c2c;
  }

  .dingzhi>>>.el-select .el-input .el-select__caret {
    color: #c72c2c;
  }

  .dingzhi>>>.el-button {
    border-radius: 0px;
    margin-left: calc(50% - 120px)
  }

  .digitalvenuedetail {
    width: 100%;
    height: auto;
  }

  .detail-bread {
    width: 1400px;
    margin: 0 auto;
    padding-top: 100px;
    padding-bottom: 60px;
  }

  .detail-bread-ul {
    width: 100%;
    height: 80px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  .detail-bread-ul span {
    font-size: 16px;
    color: #999;
    padding: 0 5px;
    display: inline-block;
  }

  .detail-bread-ul span a {
    color: rgba(153, 153, 153, .5);
  }

  .detail-bread-ul span a:hover {
    color: #c71925;
  }

  .politicsclasslistdetail-left {
    width: 74%;
  }

  .detail-title {
    width: 100%;
    padding-bottom: 30px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: flex-start;
  }

  .detail-title-1 {
    max-width: calc(100% - 200px);
    font-size: 36px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
  }

  /* width:130px; */
  .detail-title-2 {
    font-size: 20px;
    color: rgba(117, 117, 117, .8);
    background: url(../assets/img/calendar.png)no-repeat left center;
    background-size: 18px 18px;
    padding-left: 35px;
    margin-left: 35px;
  }

  .detail-video {
    width: 100%;
    height: 586px;
    overflow: hidden;
  }

  .detail-video video {
    width: 100%;
    height: 100%;
  }

  .politicsclasslistdetail-right {
    width: 24%;
  }

  .pcld-right-title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 16px;
    color: #000;
    padding-bottom: 30px;
    font-weight: bold;
  }

  .politicsclasslistdetail-right ul {
    width: 100%;
    height: 586px;
    overflow: hidden;
  }

  .politicsclasslistdetail-right ul li {
    width: 100%;
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-start;
    justify-content: space-between;
    cursor: pointer;
    margin-bottom: 20px;
  }

  .politicsclasslistdetail-right ul li:last-child {
    margin-bottom: 0;
  }


  .politicsclasslistdetail-right>>>.el-scrollbar {
    display: flex;
    justify-content: space-between;
  }

  .politicsclasslistdetail-right>>>.el-scrollbar__wrap {
    overflow-x: hidden;
    overflow-y: scroll;
    width: calc(100% + 17px);
    height: 100%;
  }


  .pcld-right-img {
    width: 112px;
    height: 77px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative;
  }

  .pcld-right-img img {
    width: 100%;
    height: auto;
  }

  .pcld-right-img span {
    display: inline-block;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.4) url(../assets/img/dig-play.png) no-repeat center center;
    background-size: 24px 24px;
  }

  .pcld-right-cont {
    width: calc(100% - 125px);
    height: 77px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    font-size: 16px;
  }

  .pcld-right-cont-name {
    width: 100%;
    color: #000;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
  }

  .pcld-right-cont-time {
    color: rgba(119, 119, 119, 0.8);
    background: url(../assets/img/calendar.png) no-repeat left center;
    background-size: 18px 18px;
    padding-left: 25px;
  }



  .detail-tuijian {
    width: 100%;
    background-color: #fff;
  }

  .digitalvenue-title {
    width: 100%;
    padding-top: 30px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-title-left {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
  }

  .digitalvenue-title-1 {
    font-size: 30px;
    color: #000;
  }

  .digitalvenue-title-right {
    background: url(../assets/img/dig-more.png) no-repeat right center;
    background-size: 14px 19px;
    padding-right: 30px;
    margin-right: 10px;
    font-size: 18px;
  }

  .digitalvenue-title-right a {
    color: #c71925;
  }

  .digitalvenue-title-right a:hover {
    color: #c71925;
    opacity: .9;
  }

  .digitalvenue-ul {
    width: 100%;
    padding-bottom: 40px;
  }

  .digitalvenue-ul ul li {
    width: 23.5%;
    margin-right: 2%;
    overflow: hidden;
    margin-top: 30px;
    cursor: pointer;
  }

  .digitalvenue-ul ul li:nth-child(4n) {
    margin-right: 0px;
  }

  .digitalvenue-ul-img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-img img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-title {
    width: 100%;
    height: 60px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-ul-title p {
    width: calc(100% - 24px);
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .digitalvenue-ul-title img {
    width: 24px;
    height: 24px;
  }

  .dingzhibox {
    width: 290px;
    height: 90px;
    margin: 70px auto;
    background-color: #c72c2c;
    font-size: 30px;
    color: #fff;
    cursor: pointer;
  }

  .dingzhi-bg {
    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    margin: auto;
    z-index: 99;
    background-color: rgba(0, 0, 0, 0.8);
  }
</style>



