			//   }, 'fast', 'linear');
			//   $(this).addClass('acinin');
			// });
			// 音视频展播
			$('.play_list ul li').hover(
				function() {
					$(this).addClass('active').stop(true, true);
					$(this).find('.inmask').stop(true, true).fadeOut();
					$(this).find('.playIcon').stop(true, true).fadeIn();
				},
				function() {
					$(this).removeClass('active').stop(true, true);
					$(this).find('.inmask').stop(true, true).fadeIn();
					$(this).find('.playIcon').stop(true, true).fadeOut();
				}
			);
			$('.cloud_box_in').hover(
				function() {
					$(this).find('.redmask').stop(true, true).fadeIn();
				},
				function() {
					$(this).find('.redmask').stop(true, true).fadeOut();
				}
			);
			// 专题列表
			$('.indextopic-list ul li').hover(
				function() {
					$(this).addClass('active').stop(true, true);
				},
				function() {
					$(this).removeClass('active').stop(true, true);
				}
			);
		},
		methods: {
			showvideo1() {
				this.videolink = ''
				this.videolink = this.oneVideo.video
				this.showvideo = true
			},
			goimg(img) {
				this.srcList = []
				this.srcList.push(img)
			},
			showvideoWindow(link) {
				console.log("看看是看是否")
				this.videolink = ''
				this.videolink = link
				this.showvideo = true

			},
			gotocloudlink(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}) // 带参跳转
				console.log("中间环节")
				window.open(routeUrl.href, '_blank');
			},
			gotoszk(id, title) {
				let routeUrl = this.$router.resolve({
					path: '/politicsclassnewsdetail',
					query: {
						id: id,
						title: title
					}
				}) // 带参跳转
				console.log("中间环节")
				window.open(routeUrl.href, '_blank');
			},
			gotopg(url) {
				window.open(url, '_blank');
			},
			gotoww(){
				var url = 'http://www.ncha.gov.cn/'
					window.open(url, '_blank');
			},
			gotoszka(id, title) {

				let routeUrl = this.$router.resolve({
					path: '/politicsclassmoredetail',
					query: {
						id: id,
						title: title
					}
				}) // 带参跳转
				console.log("中间环节")
				window.open(routeUrl.href, '_blank');
			},
			gotogmcg(id) {
				let routeUrl = this.$router.resolve({
					path: '/venuedetail',
					query: {
						id: id,
					}
				}) // 带参跳转
				console.log("中间环节")
				window.open(routeUrl.href, '_blank');
			},
			gotogroupexhibitiondetail(id, img) {
				this.$router.push({
					path: '/groupexhibitiondetail',
					query: {
						id: id,
						img: img
					}
				});
			},
			getindex(item) {
				console.log(item)
				this.value = item
				this.options1.forEach((i, x) => {
					if (i.name == item) {
						this.provinceid = i.id
					}
				});
				this.img_list = []
				this.getlist(1)
				console.log(this.provinceid)
			},
			getlist(show) {
				var that = this
				this.$http
					.post(api.user.IndexResult, {
						province: this.provinceid
					})
					.then((res) => {
						console.log(res.data.data)
						if (res.data.code == 0) {
							console.log(show, "jhhhhhhhhhhhhhhhhh")
							if (show == undefined) {
								that.$refs.fullpage.destroy(true);
							}

							// that.secondlist = res.data.data.SelectArea;
							that.banner = res.data.data.banner
							that.education = res.data.data.education
							that.zhanlanlist = res.data.data.Exhibition
							that.zhanlanlist_temp = res.data.data.Exhibition
							that.options1 = res.data.data.provinceList
							that.video = res.data.data.video
							that.oneVideo = that.video[0]
							that.stadium = res.data.data.stadium
							that.article = res.data.data.article
							that.cloud = res.data.data.cloud
							console.log(that.oneVideo)
							that.stadium.forEach((i, x) => {
								if (i.cover) {
									console.log(i.cover)
									that.img_list.push(i.cover)
								}
							});
							console.log(that.srcList)
							if (show == undefined) {
								that.$nextTick(() => {
									that.$refs.fullpage.init();
								});
							}

						} else {
							this.$message({
								message: res.data.res,
								type: "error",
							});
						}
					});
			},
			init() {
				var that = this;
				var ww = document.body.clientWidth;
				var allLength = this.loadData.length * 50 - ww - 100;
				// console.log(allLength, this.loadData.length);
				// that.activeIndex = 1;
				$('.loadingBar').animate({
					left: -(allLength + ww) + 'px'
				}, 5000);
				// setTimeout(() => {
				// 	that.activeIndex = 0;
				// }, 4500);

				setTimeout(() => {
					$('.lading_cirle').animate({
						width: '2000px',
						height: '2000px',
						'border-radius': '1000px'
					}, 500);
					$('.loading').fadeOut();
					// that.activeIndex = 0;
				}, 5000);
			},
			afterLoad(origin, destination) {
				// eslint-disable-line
				//console.log(destination.index)
				this.activeIndex = destination.index;
				this.digits = this.activeIndex + 1;
			},
			gotoPrv() {
				this.$refs.fullpage.api.moveSectionUp();
			},
			gotoNext() {
				this.$refs.fullpage.api.moveSectionDown();
			},
			gotoPage(index) {
				this.$refs.fullpage.api.moveTo(index + 1);
			},
			tourl(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}) // 带参跳转

				window.open(routeUrl.href, '_blank');
			},
			// setThis(key){
			//   console.log('wew')
			//   var hoverNow= this.$refs[key].getBoundingClientRect().top;
			//   var hovertop= this.$refs.acbox.getBoundingClientRect().top;
			//   var movepx=hoverNow-hovertop-20;
			//   $('.acbox').animate({'top':movepx+'px'},'fast');
			//   // this.acmove='transform:translateY('+movepx+'px) !important'
			// },
			zhanlannext() {
				var old = this.zhanlanlist;
				// this.activeIndex=0;
				this.zhanlanChange = false;
				var that = this;
				setTimeout(() => {
					that.zhanlanChange = true;
					// that.activeIndex=2;
				}, 200);
				this.zhanlanlist = this.zhanlanlist_temp;
				this.zhanlanlist_temp = old;
			},
			// 去登录注册
			goToUser() {
				this.$router.push({
					path: '/user'
				});
			},
		}
	};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
	.indexfadeInUp {
		-webkit-animation-name: fadeInUp;
		animation-name: fadeInUp;
		-webkit-animation-duration: 2s;
		animation-duration: 2s;
	}

	.indexfadeInUpBig {
		-webkit-animation-name: fadeInUpBig;
		animation-name: fadeInUpBig;
		-webkit-animation-duration: 2s;
		animation-duration: 2s;
	}
    
	.digitalvenue-title-right {
		width: 75px;
		background: url(../assets/img/dig-more.png) no-repeat right 7px center;
		background-size: 14px 19px;
		padding-right: 30px;
		margin-right: 10px;
		font-size: 18px;
		margin-top: 20px;
		cursor: pointer;
	}
	
	.digitalvenue-title-right a {
		color: #c71925
	}
	
	.digitalvenue-title-right a:hover {
		color: #c71925;
		opacity: .9
	}
	 .cloud_des{
		 margin-top: 20px;
	 }
	
	ul li {
		cursor: pointer;
	}
</style>



