<template>
  <div class="groupexhibition">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <ul>
            <li @click="tourl('/index')"><a>首页</a></li>
            <li class="active" @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            <li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
            <li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            <li @click="tourl('/venue')"><a>革命场馆</a></li>
            <li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>数字联展</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>数字联展</span>
        </div>
      </div>
    </div>

    <div class="groupexhibition-news">
      <div class="w1400">
        <div class="groupexhibitionmore-search flex flex-row flex-nowrap align-start justify-between">

          <el-button :class="isExhibit == 1?'gem-btn':'gem-btn active'" @click="sho(1)">正在展出</el-button>
          <el-button :class="isExhibit == 0?'gem-btn':'gem-btn active'" @click="sho(2)">往期陈列</el-button>
          <el-select v-model="pr" placeholder="省份: 全部" @change='getindex(pr)'  style="width: 150px;">
            <el-option v-for="(item,index) in sflist" :key="index" :label="item.name" :value="item.name">
            </el-option>
          </el-select>
          <el-select v-model="city" placeholder="城市: 全部" @change='getindex2(city)'  style="width: 150px;">
            <el-option v-for="(item,index) in citylist" :key="index" :label="item.name" :value="item.name">
            </el-option>
          </el-select>

          <el-date-picker
                v-model="time"
                type="date"
                placeholder="选择日期">
              </el-date-picker>


          <el-input class="search-val" v-model="input" placeholder="名称"></el-input>
       
         <el-button @click='getList' style="width: 100px;">搜索</el-button>
				   <el-button @click='clearall' style="width: 100px;">清空</el-button>

        </div>
        <div class="groupexhibition-two">
          <ul class="flex flex-row flex-wrap align-start justify-start" v-if="list != undefined && list != null && list.length > 0">
            <li v-for="(item, index) in list">
              <div class="ge-two-img" :style="'background:url('+item.cover+') center no-repeat;'">
            
                <!-- <img src="../assets/img/2.jpg">
                <p class="ge-two-zhanchu" v-if="index < 3">全景展出</p> -->
              </div>
							<div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
							    <p>收藏</p>
							  </span><span class="el-icon-star-on" v-else>
							    <p>已收藏</p>
							  </span></div>
              <p class="ge-two-title">庙堂仪范——故宫博物院藏历代人物画特展</p>
              <p class="ge-two-info">展览地点：北京中国人民军事博物馆大楼二层东</p>
              <div class="ge-two-cont flex flex-row flex-nowrap align-center justify-between">
                <p>展览时间：2021.10.29-2021.11.28</p>
                <p class="ge-two-cont-1">
                  <a @click="goToDetail(item.id)">展出</a>
                </p>
                <!-- <p class="ge-two-cont-2"><router-link to="/politicsclassnews">结束</router-link></p> -->
              </div>
            </li>
          </ul>
		  <div class="w100 flex flex-row flex-wrap align-start justify-start" v-else>
		  	<div class="scdiv">暂无数据 敬请期待！</div>
		  </div>
        </div>
        <div class="showFY">
          <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />

        </div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TablePaging from "../components/Fy";
  import api from "../api/api.js";
  export default {
    name: 'groupexhibition',
    components: {

      TablePaging,
    },
    data() {
      return {
        pageIndex: 0,
        isExhibit: 0,
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }],
        value: '',
        input: '',
        pr: null,
        city: null,
        sflist: [],
        citylist: [],
        time: '',
        currentPage: 1,
        total: 0,
        pageSize: 9,
        provinceid: '',
        cityid: '',
        list: [],
        showli: true
      }
    },
    mounted() {
      this.getSF()
      this.getList()
    },
    methods: {
      shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },
      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'exhibition',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getList()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'exhibition',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getList()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      tourl(url) {
        let routeUrl = this.$router.resolve({
          path: url,
        }) // 带参跳转

        window.open(routeUrl.href, '_blank');
      },
      sho(index) {
        if (index == 1) {
          this.isExhibit = 0
        } else if (index == 2) {
          this.isExhibit = 1
        }
      },
      getindex(item) {
        console.log(item)
        this.pr = item
        this.sflist.forEach((i, x) => {
          if (i.name == item) {
            this.city = ''
            this.citylist = i.city
            this.provinceid = i.id
          }
        });
        console.log(this.provinceid)
      },
      getindex2(item) {
        this.city = item
        this.citylist.forEach((i, x) => {
          if (i.name == item) {
            this.cityid = i.id
          }
        });
        console.log(this.cityid)
      },
      // 分页
      pageChange(value) {
        console.log(value, "hhhh");
        this.currentPage = value;
        this.getList()
      },
      getSF() {
        this.$http
          .post(api.user.sf, {

          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              this.sflist = res.data.data
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
			clearall(){
					this.pr= null,
					this.city=null,  
					this.isExhibit = 0
					this.time = ''
					this.input = ''
					this.provinceid=''
					this.cityid= ''
					     this.getList()
			},
      getList() {
        var that = this
        this.$http
          .post(api.user.MoreExhibitionList, {

            province: this.provinceid,
            displaystatus: this.isExhibit,
            city: this.cityid,
            keyword: this.input,
            limit: this.pageSize,
            page: this.currentPage,
            start: this.time,

          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              that.list = res.data.data.List
              that.total = res.data.data.totalNum

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      // 跳转详情
      goToDetail(id) {
        // this.$router.push({
        //   path: '/groupexhibitionmoredetail'
        // });
        this.$router.push({ path: '/groupexhibitiondetail', query:{ id: id}});
      },

    },
  }
</script>

<style scoped>
  ul li {
    cursor: pointer;
  }

  .groupexhibition {
    width: 100%;
    height: auto;
    background: #fff url(../assets/img/groupexhibition-bg.jpg) no-repeat center top;
    background-size: 100% 428px
  }

  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px
  }

  .groupexhibition-news {
    width: 100%;
    margin-top: 90px;
    background-color: #fff;
  }

  .groupexhibitionmore-search {
    width: 100%;
    padding-top: 60px;
  }

  .groupexhibitionmore-search>>>.gem-btn.el-button {
    background-color: #ededed;
    color: #000;
    font-size: 16px;
    border: none;
    border-radius: 2px;
  }
	.scdiv {
		width: 100%;
		height: 200px;
		display: flex;
		align-items: center;
		justify-content: center;
		font-size: 28px;
	}
  .groupexhibitionmore-search>>>.active.el-button {
    background-color: #c72c2c;
    color: #fff;
  }

  .groupexhibitionmore-search>>>.el-select {
    width: 13%;
  }

  .groupexhibitionmore-search>>>.search-val {
    width: 23%;
  }

  .groupexhibitionmore-search>>>.el-button {
    width: 10%;
  }

  .groupexhibitionmore-search>>>.el-button+.el-button {
    margin-left: 0;
  }

  .groupexhibition-two {
    width: 100%;
  }

  .groupexhibition-two ul li {
    width: 32%;
    margin-right: 2%;
    margin-top: 40px;
		    position: relative;
  }

  .groupexhibition-two ul li:nth-child(3n) {
    margin-right: 0;
  }

  .showFY {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    margin-top: 50px;
  }

  .ge-two-img {
    width: 100%;
    height: 282px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;

    background-size: cover !important;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;


  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF !important;
    font-size: 16px;
  }

  .groupexhibition-two ul li:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .ge-two-zhanchu {
    width: 60px;
    height: 30px;
    line-height: 30px;
    position: absolute;
    right: 0;
    top: 0;
    color: #fff;
    font-size: 12px;
    background: rgba(0, 0, 0, 0.6) url(../assets/img/360.png) no-repeat 15px center;
    padding-left: 50px;
    border-top-left-radius: 15px;
    border-bottom-left-radius: 15px;
    border-bottom-right-radius: 15px;
  }

  .ge-two-title {
    width: 100%;
    height: 35px;
    line-height: 35px;
    margin-top: 10px;
    font-size: 18px;
    color: #000;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .ge-two-info {
    width: 100%;
    height: 35px;
    line-height: 35px;
    font-size: 16px;
    color: #666;
    text-overflow: ellipsis;
    overflow: hidden;
    white-space: nowrap;
  }

  .ge-two-cont {
    width: 100%;
    height: 30px;
  }

  .ge-two-cont p:nth-child(1) {
    width: calc(100% - 75px);
    font-size: 16px;
    color: #666;
  }

  .ge-two-cont-1,
  .ge-two-cont-2 {
    width: 60px;
  }

  .ge-two-cont-1 a {
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: #c72c2c;
    font-size: 15px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
  }

  .ge-two-cont-1 a:hover {
    opacity: 0.9;
  }

  .ge-two-cont-2 a {
    display: block;
    width: 100%;
    height: 30px;
    line-height: 30px;
    background-color: #999;
    font-size: 15px;
    color: #fff;
    text-align: center;
    border-radius: 5px;
  }
</style>



