import Vue from 'vue'
import Router from 'vue-router'
import Index from '@/components/index'                                          // 首页
import Venue from '@/components/venue'                                          // 革命场馆
import Venuedetail from '@/components/venuedetail'                              // 革命场馆-详情
import DigitalVenue from '@/components/digitalvenue'                            // 数字传播
import DigitalVenueList from '@/components/digitalvenuelist'                    // 数字传播-列表
import DigitalVenueDetail from '@/components/digitalvenuedetail'                // 数字传播-详情
import PersonalCenter from '@/components/personalcenter'                        // 个人中心
import PoliticsClass from '@/components/politicsclass'                          // 思政课
import PoliticsClassMore from '@/components/politicsclassmore'                  // 思政课-系列列表
import PoliticsClassList from '@/components/politicsclasslist'                  // 思政课-列表
import PoliticsClassMoreDetail from '@/components/politicsclassmoredetail'      // 思政课-列表-详情
import PoliticsClassNews from '@/components/politicsclassnews'                  // 思政课-新闻资讯列表
import PoliticsClassNewsDetail from '@/components/politicsclassnewsdetail'      // 思政课-新闻资讯列表-详情
import GroupExhibition from '@/components/groupexhibition'                      // 数字联展
import GroupExhibitionMore from '@/components/groupexhibitionmore'              // 数字联展-列表
import GroupExhibitionNews from '@/components/groupexhibitionnews'              // 数字联展-新闻资讯列表
import GroupExhibitionDetail from '@/components/groupexhibitiondetail'          // 数字联展-列表-详情
import User from '@/components/user'                                            // 登录、注册

Vue.use(Router)

let kejianrouter = new Router({
  // mode: 'history',
  routes: [
    {
      path: '*',
      name: 'Index',
      component: Index,
    },
    {
      path: '/',
      name: 'Index',
      component: Index,
    },
    {
      path: '/venue',
      name: 'venue',
      component: Venue,
    },
    {
      path: '/venuedetail',
      name: 'venuedetail',
      component: Venuedetail,
    },
    {
      path: '/digitalvenue',
      name: 'digitalvenue',
      component: DigitalVenue,
    },
    {
      path: '/digitalvenuelist',
      name: 'digitalvenuelist',
      component: DigitalVenueList,
    },
    {
      path: '/digitalvenuedetail',
      name: 'digitalvenuedetail',
      component: DigitalVenueDetail,
    },
    {
      path: '/personalcenter',
      name: 'personalcenter',
      component: PersonalCenter,
      meta: {
        auth: true,
      },
    },
    {
      path: '/politicsclass',
      name: 'politicsclass',
      component: PoliticsClass,
    },
    {
      path: '/politicsclassmore',
      name: 'politicsclassmore',
      component: PoliticsClassMore,
    },
    {
      path: '/politicsclasslist',
      name: 'politicsclasslist',
      component: PoliticsClassList,
    },
    {
      path: '/politicsclassmoredetail',
      name: 'politicsclassmoredetail',
      component: PoliticsClassMoreDetail,
    },
    {
      path: '/politicsclassnews',
      name: 'politicsclassnews',
      component: PoliticsClassNews,
    },
    {
      path: '/politicsclassnewsdetail',
      name: 'politicsclassnewsdetail',
      component: PoliticsClassNewsDetail,
    },
    {
      path: '/groupexhibition',
      name: 'groupexhibition',
      component: GroupExhibition,
    },
    {
      path: '/groupexhibitionmore',
      name: 'groupexhibitionmore',
      component: GroupExhibitionMore,
    },
    {
      path: '/groupexhibitionnews',
      name: 'groupexhibitionnews',
      component: GroupExhibitionNews,
    },
    {
      path: '/groupexhibitiondetail',
      name: 'groupexhibitiondetail',
      component: GroupExhibitionDetail,
    },
    {
      path: '/user',
      name: 'user',
      component: User,

    },
  ]
});
// 判断是否需要登录权限 以及是否登录
kejianrouter.beforeEach((to, from, next) => {
  // 判断是否需要登录权限
  if (to.matched.some((res) => res.meta.requireAuth) || to.meta.auth) {
    // 判断是否登录
    if (localStorage.getItem("token")) {
          next();
    }else {
      // 没登录则跳转到登录界面
      next({
        path: "/user",
        query: {
          redirect: to.fullPath,
        },
      });
    }
  } else {
    next();
  }
});
export default kejianrouter;



