<template>
  <div class="politicsclass">
    <div class="new_header header_brotop active">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <ul>
            <li @click="tourl('/index')"><a>首页</a></li>
            <li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            <li class="active" @click="tourl('/politicsclass')"><a>思政课</a></li>
            <li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            <li @click="tourl('/venue')"><a>革命场馆</a></li>
            <li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>思政课</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>思政课</span>
          <span>/</span>
          <span>列表</span>
        </div>
      </div>
    </div>

    <div class="politicsclasslist-search">
      <div class="w1400 flex flex-row flex-nowrap align-center justify-between">
        <el-select v-model="pr" placeholder="省份: 全部" @change='getindex(pr)'>
          <el-option v-for="(item,index) in sflist" :key="index" :label="item.name" :value="item.name">
          </el-option>
        </el-select>
        <el-select v-model="city" placeholder="城市: 全部" @change='getindex2(city)'>
          <el-option v-for="(item,index) in citylist"  :key="index" :label="item.name" :value="item.name">
          </el-option>
        </el-select>
     <el-date-picker
                v-model="time"
                type="date"
                placeholder="选择日期">
              </el-date-picker>


        <el-input class="search-val" v-model="input" placeholder="名称"></el-input>
				 
        <el-button @click='getList'>搜索</el-button>
		<el-button @click='clearall' style="width: 100px;">清空</el-button>
      </div>
    </div>
    <div class="politicsclasslist">
      <div class="w1400">
        <div class="personalcenter_four">
          <ul class="w100 flex flex-row flex-wrap align-start justify-start"  v-if="list != undefined && list != null && list.length > 0" >
            <li v-for="(item, index) in list" >
              <div class="personalcenter_four_img showulung">
                <!--            <img src="../assets/img/p3.jpg"><span>收藏</span> -->
                <div class="fm" :style="'background:url('+item.cover+') center no-repeat;'" @click="goTodetail(item.id,item.title)">


                </div>
                <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
                    <p>收藏</p>
                  </span><span class="el-icon-star-on" v-else>
                    <p>已收藏</p>
                  </span></div>
              </div>
              <div class="personalcenter_four_title">{{item.title}}</div>
              <div class="personalcenter_four_intro">{{item.describe}}</div>
            </li>
          </ul>
		  <div class="w100 flex flex-row flex-wrap align-start justify-start" v-else>
		  	<div class="scdiv">正在加载中！</div>
		  </div>
          <div class="showFY">
            <tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize" @pageChange="pageChange" />

          </div>
        </div>
      </div>
    </div>



    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
          <ul>
            <li>
              <router-link to="/index">红色足迹</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/groupexhibition">数字联展</router-link>
            </li>
            <li>|</li>
            <li>
              							<router-link to="/groupexhibitionnews">联展资讯</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/digitalvenue">数字联播</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/venue">革命场馆</router-link>
            </li>
            <li>|</li>
            <li>
              <router-link to="/user">个人中心</router-link>
            </li>
          </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              <img src="../assets/img/ewm.png" alt="">
            </div>
          </div>
          <div class="foot_tel">
            <!-- <router-link to="/index"></router-link> -->
            <div class="showlg"></div>
            <div class="show_div">
              待更新
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
  import TablePaging from "../components/Fy";
  import api from "../api/api.js";
  import $ from 'jquery';
  export default {
    name: 'politicsclasslist',
    components: {

      TablePaging,
    },
    data() {
      return {
        pageIndex: 0,
        options: [{
          value: '选项1',
          label: '黄金糕'
        }, {
          value: '选项2',
          label: '双皮奶'
        }, {
          value: '选项3',
          label: '蚵仔煎'
        }, {
          value: '选项4',
          label: '龙须面'
        }, {
          value: '选项5',
          label: '北京烤鸭'
        }],
        value: '',
        input: '',
        pr: null,
        city: null,
        sflist: [],
        citylist: [],
        time: '',
        currentPage: 1,
        total: 0,
        pageSize: 9,
        provinceid: '',
        cityid: '',
        list: [],
        showli: true
      }
    },
    mounted() {
      // $('.personalcenter_four ul li').hover(
      //   function() {
      //     $(this).addClass('active');
      //   },
      //   function() {
      //     $(this).removeClass('active');
      //   }
      // );
      this.getSF()
      this.getList()
    },
    methods: {
      goTodetail(id,title) {
        // this.$router.push({
        //   path: '/politicsclasslistdetail',

        // });
              this.$router.push({ path: '/politicsclassmore', query:{ id: id,title:title}});
      },
      tourl(url) {
        let routeUrl = this.$router.resolve({
          path: url,
        }) // 带参跳转

        window.open(routeUrl.href, '_blank');
      },
      getList() {
        var that = this
        this.$http
          .post(api.user.MoreVideoList, {

            province: this.provinceid,
            city: this.cityid,
            keyword: this.input,
            limit: this.pageSize,
            page: this.currentPage,
            start: this.time,
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              that.list = res.data.data.list
              that.total = res.data.data.totalNum

            }else if(res.data.code == -100){
              localStorage.clear();
              that.getList()
							 that.showli = false
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      getSF() {
        this.$http
          .post(api.user.sf, {

          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {

              this.sflist = res.data.data
            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      getindex(item) {
        console.log(item)
        this.pr = item
        this.sflist.forEach((i, x) => {
          if (i.name == item) {
            this.city = ''
            this.citylist = i.city
            this.provinceid = i.id
          }
        });
        console.log(this.provinceid)
      },
      getindex2(item) {
        this.city = item
        this.citylist.forEach((i, x) => {
          if (i.name == item) {
            this.cityid = i.id
          }
        });
        console.log(this.cityid)
      },
      // 分页
      pageChange(value) {
        console.log(value, "hhhh");
        this.currentPage = value;
        this.getList()
      },
	  clearall(){
	  		this.pr= null,
	  		this.city=null,  
	  		this.time = ''
	  		this.input = ''
	  		this.provinceid=''
	  		this.cityid= ''
			this.getList()
	  },
      shouc(item) {
             console.log(item, "剪辑")
             if (item.is_collect == "true") {
               this.nosc(item.id)
             } else {
               this.oksc(item.id)
             }
           },
           oksc(id) {
             var that = this
             this.$http
               .post(api.user.UserAddCollect, {
                 module: 'education',
                 id: id
               })
               .then((res) => {
                 console.log(res)
                 if (res.data.code == 0) {
                   this.$message({
                     message: '收藏成功',
                     type: "success",
                   });
                   that.getList()

                 } else {
                   this.$message({
                     message: res.data.msg,
                     type: "error",
                   });
                 }
               });
           },
           nosc(id) {
             var that = this
             this.$http
               .post(api.user.UserDelCollect, {
                 module: 'education',
                 id: id
               })
               .then((res) => {
                 console.log(res)
                 if (res.data.code == 0) {
                   this.$message({
                     message: '取消成功',
                     type: "success",
                   });
                   that.getList()

                 } else {
                   this.$message({
                     message: res.data.msg,
                     type: "error",
                   });
                 }
               });
           },

    },
  }
</script>

<style scoped>
  .personalcenter_four_img .fm {
    width: 100%;
    height: 280px;
    background-size: cover !important;
    position: relative;
  }

  .personalcenter_four_img:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .showulung {
    position: relative;
  }

  .showFY {
    width: 100%;
    height: 50px;
    display: flex;
    align-items: center;
    justify-content: center;
    margin-bottom: 50px;
    margin-top: 50px;
  }

  .politicsclass {
    width: 100%;
    height: auto;
    background: #f5f5f5 url(../assets/img/politicsclass-bg.jpg) no-repeat center top;
    background-size: 100% auto
  }

  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px
  }

  .politicsclasslist-search {
    width: 100%;
    background-color: #f5f5f5;
    margin-top: 90px;
    padding-top: 40px;
  }

  .politicsclasslist-search>>>.el-select {
    width: 15%;
  }

  .politicsclasslist-search>>>.search-val {
    width: 30%;
  }

  .politicsclasslist-search>>>.el-button {
    width: 10%;
  }

  .politicsclasslist {
    width: 100%;
  }

  .personalcenter_four {
    width: 100%;
    padding-bottom: 20px
  }

  .personalcenter_four ul li {
    width: 32%;
    margin-top: 30px;
    margin-right: 2%;
    cursor: pointer;
  }

  .personalcenter_four ul li:nth-child(3n) {
    margin-right: 0
  }

  .personalcenter_four_img {
    width: 100%;
    height: 280px;
    overflow: hidden;
    display: flex;
    align-items: center;
    justify-content: center;
    position: relative
  }

  .personalcenter_four_img img {
    width: 100%;
    min-height: 280px
  }

 /* .personalcenter_four ul li .personalcenter_four_img span {
    position: absolute;
    top: 20px;
    right: 20px;
    display: inline-block;
    opacity: 0;
    background: rgba(0, 0, 0, .5) url(../assets/img/star.png) no-repeat 5px center;
    color: #fff;
    width: 40px;
    height: 32px;
    line-height: 32px;
    overflow: hidden;
    font-size: 16px;
    padding-left: 25px;
    border-radius: 3px;
    cursor: pointer
  } */

  .personalcenter_four ul li.active .personalcenter_four_img span {
    opacity: 1
  }

  .personalcenter_four_title {
    width: 100%;
    height: 50px;
    line-height: 50px;
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis
  }

  .personalcenter_four_intro {
    font-size: 16px;
    color: #666;
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden
  }

  ul li {
    cursor: pointer;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;


  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF !important;
    font-size: 16px;
  }
</style>



