
<template>
  <div class="pageGroup">
    <el-button size="small" @click="pervious" class="but-left"><img src="../assets/img/prev.png" ></el-button>
    <el-pagination
      @size-change="handleSizeChange"
      @current-change="handleCurrentChange"
      :current-page.sync="current"
      :page-size="pageSize"
      layout="pager"
      :total="total"
      ref="pageGroup">
    </el-pagination>
    <el-button size="small" @click="next"><img src="../assets/img/next.png" ></el-button>
  </div>
</template>

<script>
export default {
  name: 'TablePaging',
  props: {
    currentPage: Number,
    pageSize: Number,
    total: Number,
  },
  data() {
    return {
      current: 1,
    }
  },
  watch: {
    currentPage: {
      handler() {
        this.current = this.currentPage
        console.log(this.current,this.pageSize,"kkkkkkkkkk")
      },
      deep: true,
    },
  },
  created() {
    this.current = this.currentPage
  },
  methods: {
    handleSizeChange(val) {
      console.log(val)
    },
    handleCurrentChange(val) {
      //console.log(val)
      this.$emit('pageChange', val)
    },
    pervious() {
      this.$refs.pageGroup.prev()
    },
    next() {
      this.$refs.pageGroup.next()
    },
  },
}
</script>

<style lang="scss" scoped>
  .but-left{

  }
.el-pager li {
  font-size: 12px;
  height: 24px;
  line-height: 24px;
  padding: 0 5px;
  box-sizing: border-box;
}
.pageGroup{
	display: flex;
	align-items: center;
	height: 50px;

	border-radius: 4px;
}
.el-pager li{
	background: red !important;
}
.el-button{
	border: none !important;
	background: none !important;
	font-size: 16px;
	font-family: Source Han Sans CN;
	font-weight: 400;
	color: #2C3B6F;
}
</style>



