<template>
	<div class="content groupexhibitiondetail">
		<div class="new_header header_brotop active">
			<div class="flex flex-row flex-nowrap align-center space-between w1400">
				<div class="logo" ref="logo"></div>
				<div class="menu-bar">
					<ul>
						<li @click="tourl('/index')"><a>首页</a></li>
						<li class="active" @click="tourl('/groupexhibition')">
							<a>数字联展</a>
						</li>
						<li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
						<li @click="tourl('/digitalvenue')"><a>数字传播</a></li>
						<li @click="tourl('/venue')"><a>革命场馆</a></li>
						<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
					</ul>
				</div>
			</div>
			<div class="audio">
				<div style="width: 100%;height: 100%;">
					<div style="width: 100%; height: 50px"></div>
					<div class="player">
						<!-- <video id="myVideo" ref="video" :src="videoSrc"></video> -->

						<!-- <div class="controls" ref="video1">
						
							<a ref="isPlay" @click="playAndPause" :class="videook?'switch':'expand'"></a>
							<div ref="video3" :class="!showCSS3?'':'Css3'">{{listtitle}}</div>
						</div> -->
					</div>
				</div>
			</div>
		</div>
		<div class="main_content">
			<full-page ref="fullpage" :options="options">
				<div class="section" v-for="(item, index) in secondlist" :key="index">
					<div class="groupexhibitiondetail-first">

 
						<div class="firstimg" :style="{
				    background: 'url(' + item.img + ') center no-repeat',
				  }"> </div>

						<div class="ged-cont" v-if="index === 0&&noone == false">
							<div class="ged-contWidth1">
								<div class="ged-cont-head fadeInUp animated"
									style="-webkit-animation-duration: 1s; animation-duration: 1s">
									<!-- <img :src="listone.logo"> -->
									<div class="showBt">
										<h3>{{ listone.title }}</h3>
									<!-- 	<h4>
											{{listone.subtitle}}
										</h4> -->
									</div>
								</div>
								<p class="showH4 ged-cont-introduction fadeInUp animated"
									style="-webkit-animation-duration: 2s; animation-duration: 2s">
									{{ listone.describe }}
								</p>
								<p class="ged-cont-information icon-address fadeInUp animated"
									style="-webkit-animation-duration: 3s; animation-duration: 3s;color: #fff !important;">
									展览地点：{{ listone.location }}
								</p>
								<p class="ged-cont-information icon-date fadeInUp animated"
									style="-webkit-animation-duration: 4s; animation-duration: 4s;color: #fff !important;padding-bottom: 30px;">
									展览时间：{{ listone.holding_time }}
								</p>
							</div>
						</div>
						<div class="ged-cont" v-if="index === 0 && noone == true">
							<div class="ged-contWidth">
								<div class="ged-cont-head fadeInUp animated"
									style="-webkit-animation-duration: 1s; animation-duration: 1s">
									<div class="showContenttitle">
										<p>{{secondlist[activeIndex].title}}</p>
										<div class="showck" @click="islook = true">查看</div>
									</div>
									<div class="showaudeo">
										<audio ref="audio" :src="videoSrc" autoplay="autoplay" preload="auto" metadata
											controls="controls" loop="loop"></audio>
									</div>
								</div>
							</div>
						</div>

					</div>
				</div>
			</full-page>
			<div class="groupexhibitiondetail-footer flex flex-row flex-nowrap align-end"
				:class="activeIndex > 0 ? 'justify-end' : 'justify-center'">
				<div class="ged-list-small fadeInRight animated" v-if="activeIndex > 0 && noone == false">

					<div class="ged-list-fast" @click="isRightMore = true">
						<span>展览目录</span>
						<img src="../assets/img/second-right.jpg" />
					</div>
				</div>
				<div class="ged-list-small fadeInRight animated" v-if="activeIndex >= 0 && noone == true">
					<div class="ged-list-fast">
						<span>展览目录</span>
						<img src="../assets/img/second-right.jpg" />
					</div>
				</div>
				<div class="ged-list ">

					<div class="showActive1" v-if="activeIndex == 0 && noone == false">
						<div class="el-icon-d-arrow-right showDowon"></div>向下滚动查看更多
					</div>
					<ul class="showgedlist">
						<li v-for="(item, index) in secondlist" class="fadeInRight animated"
							:class="activeIndex == index ? 'active' : ''" :style="`animation-duration: ${index + 1}s`"
							@click="fullClick(index)">
							<div class="ged-list-small-img">
								<!-- <img :src="item.img"> -->
								<div class="ged-list-small-imgBg" :style="{
				    background: 'url(' + item.img + ') center no-repeat',
				  }">

									<div
										style="display: flex;align-items: center;width: 100%;height: 100%;background-color: rgba(0, 0, 0, 0.5);">
										<p
											style="color: #fff;width: 100%;text-align: center;padding-left: 5px;padding-right: 5px;">
											{{item.title}}</p>
									</div>
								</div>

							</div>

							<!-- <span>展区{{ index + 1 }}</span> -->
						</li>
					</ul>
				</div>

				<!-- 有序章 -->

				<!-- 没序章 -->

			</div>
			<div>

			</div>
			<div class="showContent" :class="secondlist[activeIndex].contentTT?'TT':''"
				v-if="activeIndex != 0&&(secondlist[activeIndex].content!==null)">
				<!-- 显示展品 -->
				<div class="activedvi" v-show="secondlist[activeIndex].copy=='false'">
					<div class="activedvial">
						<div style="text-align: center;font-size: 25px;color:#ffffff;letter-spacing: 10px;">展品</div>
						<div class="showimg3a" :class="secondlist[activeIndex].center==true?'show33':''">
							<div style="width: 150px;margin-right: 50px;" :class="secondlist[activeIndex].center==true?'show33':''"
								v-for="(item, index) in secondlist[activeIndex].SelectStand">
								<div class="showAimgs">
									<div class="showimgall" :style="'background:url('+item.images+') center no-repeat;'"
										@click="detailClick(item,index)">

									</div>
									<div class="showimgalltitle">{{item.stand_title}}</div>
								</div>
								
							</div>
						</div>
					</div>


				</div>
				<div class="ged-contWidth" v-if="activeIndex + 1 !== indexLength">
					<div class="ged-cont-head fadeInUp animated"
						style="-webkit-animation-duration: 1s; animation-duration: 1s">
						<div class="showContenttitle">
							<p>{{secondlist[activeIndex].title}}</p>
							<div class="showck" @click="islook = true">查看</div>
						</div>
						<div class="showaudeo">
							<div class="showfd">
								<el-image style="width: 100%; height: 100%;opacity: 0.01;"
									:src='secondlist[activeIndex].img' :preview-src-list="srcListtwo">
								</el-image>
								<img src="../assets/img/fd.png" style="width: 33px;max-width: 33px !important;" />
							</div>

							<audio ref="audio" :src="videoSrc" autoplay="autoplay" preload="auto" metadata
								controls="controls" loop="loop"></audio>
						</div>

					</div>

				</div>


				<div class="showindexnith" v-else>
					<div class="showtitleimg" v-if="secondlist[activeIndex].titleimgshow"
						:style="'background:url('+secondlist[activeIndex].titleimgshow+') center no-repeat;'"></div>
					<div class="showContenttitle" v-else>{{secondlist[activeIndex].title}}</div>

					<div class="showimg2" v-if="secondlist[activeIndex].contentTT" style="color: #000 !important;">
						{{secondlist[activeIndex].contentTT}}
					</div>
					<div class="showimg3" v-if="secondlist[activeIndex].contentTT">
						<div class="showAimg" v-for="(item, index) in listcenter.slice(0,3)">
							<div class="showimgall" style="width: 100%;border: none;background-size: contain !important;">
								<div class="showimgback" :style="'background:url('+item.cover+') center no-repeat;'"
									@click="goToDetail(item.id,item.insideimg)">

								</div>
							</div>
							<div class="showimgalltitle" style="color: #000 !important;">{{item.title}}</div>
							<div class="showimgalladdress" style="color: #000 !important;">展览地点:{{item.location}}</div>
							<div class="showimgalladdress" style="color: #000 !important;">展览时间:{{item.holding_time}}
							</div>
						</div>
					</div>
				</div>
			</div>
		</div>
		<div class="showTab" v-if="isInformation">
			<div class="ged-information fadeInRight animated">
				<div class="top_show_title">
					<span>{{showTitle}}</span>
					<div class="
				              ged-information-close
				              flex flex-row flex-nowrap
				              align-center
				              justify-start
				            " @click="closedInformation">
						<i class="el-icon-close"></i>
					</div>

				</div>
				<div class="ged-information-scroll">
					<!-- <el-scrollbar style="height: 100%"> -->
						<div class="showimg_big">
							<el-image style="width: 100px; height: 100px" :src='content_img'
								:preview-src-list="srcList">
							</el-image>
						</div>
						<div class="showimg" v-html="content"></div>
					<!-- </el-scrollbar> -->
				</div>
			</div>

			<div class="groupexhibitiondetail-souse fadeInUp animated infinite" v-if="activeIndex == 0">
				<img src="../assets/img/ged-mouse.png" />
			</div>
		</div>

		<div class="showTab" v-if="islook">
			<div class="ged-information fadeInRight animated">
				<div class="top_show_title">
					<span>{{secondlist[activeIndex].title}}</span>
					<div class="
								      ged-information-close
								      flex flex-row flex-nowrap
								      align-center
								      justify-start
								    " @click="closedInformation">
						<i class="el-icon-close"></i>
					</div>

				</div>
				<div class="ged-information-scroll">
					<el-scrollbar style="height: 100%">
						<div class="ged-cont-head fadeInUp animated">
							<div class="showContenttitle" style="border-right: none;">
								<!-- {{secondlist[activeIndex].title}} -->

							</div>

						</div>
						<div class="showimg1  ged-cont-introduction fadeInUp animated"
							v-html="secondlist[activeIndex].content"></div>
						<div class="showimg2 ged-cont-information icon-address fadeInUp animated"
							v-if="secondlist[activeIndex].contentTT">{{secondlist[activeIndex].contentTT}}
						</div>
					</el-scrollbar>
				</div>
			</div>

			<!-- 	<div class="groupexhibitiondetail-souse fadeInUp animated infinite" v-if="activeIndex == 0">
				<img src="../assets/img/ged-mouse.png" />
			</div> -->
		</div>
	</div>

	</div>
</template>

<script>
	import api from "../api/api.js";
	import $ from "jquery";
	import VueSlickCarousel from "vue-slick-carousel";
	import "vue-slick-carousel/dist/vue-slick-carousel.css";
	// optional style for arrows & dots
	import "vue-slick-carousel/dist/vue-slick-carousel-theme.css";
	import Aplayer from 'vue-aplayer'
	export default {
		name: "groupexhibitiondetail",
		data() {
			return {
				id: "",
				list: [],
				options: {
					licenseKey: "YOUR_KEY_HEERE 您花钱买的key值",
					afterLoad: this.afterLoad,
					keyboardScrolling: true,
					// menu: '#menu',
					controlArrows: false,
					scrollBar: false,
					normalScrollElements: ".ged-information-scroll, .el-image-viewer__wrapper",
				},
				activeIndex: 0,
				footIndex: 0, // 当前展示页面下标
				detailIndex: -1, // 楼层列表展示下标
				isRightMore: false, // 右侧隐藏
				isInformation: false, // 右侧信息展示
				infoImg: require("../assets/img/amplifier.png"),
				srcList: [],
				totalTime: null,
				inAn: true,
				currPlayTime: '00:00:00',
				videoSrc: '',
				List: [
					require("../assets/img/3.jpg"),
					require("../assets/img/4.jpg"),
					require("../assets/img/h4.jpg"),
				],

				secondIndex: 0,
				secondlist: [],
				listzw: [],
				listone: [],
				content: null,
				fbt: "",
				showok: false,
				videook: true,
				showCSS3: false,
				content_img: null,
				showLeftdiv: false,
				currentSrc: '',
				listtitle: '',
				noone: false,
				islook: false,
				srcListtwo: [],
				indexLength: '',
				listcenter: [],
				nums: 0,
				showloading: false,
				showTitle: '详细介绍',
				setting: {
					autoplay: false,
					infinite: true,
					initialSlide: 0,
					speed: 400,
					slidesToShow: 0,
					slidesToScroll: 1,
					swipeToSlide: true,
				},
				loading: null
			};
		},
		components: {
			VueSlickCarousel,
		},
		computed: {},
		mounted() {
			this.id = this.$route.query.id;
			this.ftb = this.$route.query.img;
			this.showloading = true
			this.loading = this.$loading({
				lock: true,
				text: '正在加载中...',
				spinner: 'el-icon-loading',
				background: '#b5001f'
			});
			
			this.getlist();
			this.getlistcenter()
			// this.videoPlay();

		},
		methods: {
			audioClick(ref) {
				let _this = this
				let audio = this.$refs[ref] //获取组件
				if (this.list.audioStatus && this.list.audioStatus == 'playing') {
					//当前组件正在播放 则暂停播放
					_this.$set(this.list, 'played', false) //记录播放还是暂停的状态
					_this.$set(this.list, 'audioStatus', 'pause') //
					audio.pause()
				} else {
					//如果没有播放状态 则开始播放
					this.$set(this.list, 'played', true)
					this.$set(this.list, 'audioStatus', 'playing')
					audio.play()
					audio.addEventListener("ended", function() {
						//监听播放播放结束后 重置播放状态为初始状态
						_this.$set(_this.list, 'played', false)
						_this.$set(_this.list, 'audioStatus', 'pause')
						audio.pause()
					});
				}
			},
			// 跳转详情
			getlist() {
				var that = this;
				this.$http
					.post(api.user.ExhibitionInfo, {
						exhibition_id: this.id,
					})
					.then((res) => {
						// console.log(res)
						if (res.data.code == 0) {
							that.$refs.fullpage.destroy(true);

							that.secondlist = res.data.data.SelectArea;
							// that.$refs.fullpage.api.setAllowScrolling(false)
						
							let list = [res.data.data.info.cover];
							that.secondlist.slice(0, 3).forEach(i => {
								list.push(i.img)
							})
							that.secondlist.forEach(i => {
								console.log(i.SelectStand.length,"执行力")
								if(i.SelectStand.length == 1){
									i.center = true
								}else{
									i.center = false
								}
							})
							that.isLoaded(list)
							that.secondlist.push({
								'title': '谢谢观看',
								"contentTT": "谢谢观看,浏览结束,您还可关注其他展览",
								"SelectArea": [],
								// "img": require("../assets/img/szkbg.png"),
								"img": require("../assets/img/userleft.jpg"),
								"titleimgshow": require("../assets/img/xxgk.png"),
								'audio': '',
							})
							if (res.data.data.info.insideimg_status == 1) {
								that.secondlist.unshift({
									"title": '序章',
									"SelectArea": [],
									"img": res.data.data.info.insideimg,
									'audio': '',
								})
							} else {
								that.noone = true
							}
							console.log("that.secondlistthat.secondlist",that.secondlist)
							that.srcListtwo.push(that.secondlist[that.activeIndex].img)
							that.indexLength = that.secondlist.length
							console.log(that.indexLength)
							// that.$refs.video.src = res.data.data.SelectArea[0].audio
							that.videoSrc = res.data.data.SelectArea[0].audio
							that.listtitle = res.data.data.SelectArea[0].title
							that.validateTextLength(that.listtitle)
							// that.$nextTick(() => {
							// 	that.$refs.video.load()
							// 	that.$refs.video.play()
							// });
							console.log(that.$refs.video, that.videoSrc)



							that.showok = true
							// var img = ['img':that.fbt]
							// that.secondlist.unshift(img)
							that.$nextTick(() => {
								that.$refs.fullpage.init();
							});
							console.log(that.secondlist.length);
							that.listone = res.data.data.info;
							that.fbt = Array.from(that.listone.subtitle);
						} else if (res.data.code == -100) {
							localStorage.clear();
							that.getlist();
						} else {
							this.$message({
								message: res.data.res,
								type: "error",
							});
						}
					});
			},
			validateTextLength(value) {
				// 中文、中文标点、全角字符按1长度，英文、英文符号、数字按0.5长度计算
				let cnReg = /([\u4e00-\u9fa5]|[\u3000-\u303F]|[\uFF00-\uFF60])/g
				let mat = value.match(cnReg)
				let length
				let alllength
				if (mat) {
					length = (mat.length + (value.length - mat.length) * 0.5)
					alllength = length

				} else {
					alllength = value.length * 0.5

				}
				if (alllength > 5) {
					this.showCSS3 = true
				} else {
					this.showCSS3 = false
				}

			},
			goToDetail(id, img) {
				// console.log(link)
				// this.$router.push({
				//   path: `/${link}`
				// });
				this.id = id;
				this.ftb = img;
				this.getlist();
				this.getlistcenter()
				this.videoPlay();
			},
			showimgfd() {

				this.$refs.preview.clickHandler()
			},
			// 滚动到某一屏后的回调函数(两个参数 第一个是锚链接的名称 第二个是索引值)
			afterLoad(origin, destination) {

				this.activeIndex = destination.index; // 当前页面下标
				this.inAn = true
				this.footIndex = destination.index; // 恢复展区默认值
				this.isInformation = false; // 关闭打开的展位
				this.isRightMore = false; // 收起右侧楼层
				this.detailIndex = -1; // 恢复展位默认值
				this.srcList = []
				console.log(origin, destination, "看看看看");
				this.listtitle = this.secondlist[this.activeIndex].title
				this.validateTextLength(this.listtitle)
				this.videoSrc = this.secondlist[this.activeIndex].audio
				this.srcListtwo = []
				this.srcListtwo.push(this.secondlist[this.activeIndex].img)
				// this.$nextTick(() => {
				// 	this.$refs.video.load()
				// 	this.$refs.video.play()
				// });
				// if (this.secondlist[this.activeIndex].audio == "") {
				// 	this.videook = false
				// 	this.listtitle = "暂无音乐"
				// } else {
				// 	this.videook = true
				// }
				this.showzw(destination.index);


			},
			// 翻页
			fullClick(index) {
				this.$refs.fullpage.api.moveTo(index + 1);

			},
			handlePreClick() {
				console.log("1")
			},
			handleNextClick() {
				console.log("2")
			},
			// 向上翻页
			clickPrev() {
				this.$refs.fullpage.api.moveSectionUp();
			},
			// 向下翻页
			clickNext() {
				this.$refs.fullpage.api.moveSectionDown();
			},
			// 底部小图点击
			detailClick(item, detailId) {
				console.log(item, "jhhgg");
				this.detailIndex = detailId;
				this.content = item.content;
				this.content_img = item.images;
				this.srcList.push(this.content_img)
				this.isInformation = true;
				this.isRightMore = false;
			},
			// 关闭右侧楼层
			closedInformation() {
				this.isInformation = false;
				this.islook = false
				this.srcList = []
				this.detailIndex = -1;
			},
			showzw(index) {
				console.log(index, "急急急");

				this.listzw = this.secondlist[index].SelectStand;
				console.log(this.listzw, "急急急111");
				if (this.listzw == undefined) {
					this.setting.slidesToShow = 0
				} else if (this.listzw.length > 4) {
					this.setting.slidesToShow = 4
				} else {
					this.setting.slidesToShow = this.listzw.length
				}
				setTimeout(() => {
					document.getElementsByClassName('showgedlist')[0].scrollLeft = 260 * index
				}, 0)
			},
			tourl(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}); // 带参跳转

				window.open(routeUrl.href, "_blank");
			},

			// 进入视频状态
			videoPlay() {
				let self = this

				// 当视频可以播放的时候
				this.$refs.video.oncanplay = function() {
					this.style.display = "block"; // 显示视频
				};

				// 播放完毕还原设置
				var that = this
				this.$refs.video.onended = function() {
					that.playAndPause()
				};
			},
			getlistcenter() {
				var that = this
				this.$http
					.post(api.user.EndRecommend, {
						exhibition_id: this.id,
					})
					.then((res) => {
						if (res.data.code == 200) {
							that.listcenter = res.data.data.list
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			isLoaded(arr) {
				let that = this;
				let img = [],
					flag = 0;
				let imgTotal = arr.length;
				console.log(arr[0], "第一张图")
				for (let i = 0; i < imgTotal; i++) {
					img[i] = new Image()
					console.log(img[i], "执行到了")
					img[i].src = arr[i]
					img[i].onload = function() {
						//第i张图片加载完成
						flag++
						that.nums = parseInt((flag / imgTotal) * 100)
						that.loading.setText(`已加载 ${that.nums}%`)
						console.log(flag, that.nums, "每一次")

						if (flag == imgTotal) {
							//全部加载完成
							that.nums == 100
							that.showloading = false
							console.log(111, '加载完成');
							that.loading.close()
							// that.$refs.fullpage.api.setAllowScrolling(true)
						}
					}
				}
			},
			// 播放/暂定
			playAndPause() {
				let myVideo = document.getElementById("myVideo");
				if (myVideo.paused) {
					console.log("看1")
					this.videook = true
					myVideo.play();
				} else {
					console.log("看2")
					this.videook = false
					myVideo.pause();
				}
			},

			// 跳跃播放
			jumpProgress(e) {
				let myVideo = document.getElementById("myVideo");
				var event = e || window.event;
				myVideo.currentTime =
					(event.offsetX / this.$refs.progress.offsetWidth) * myVideo.duration;
			},

			// 全屏
			fullScreen() {
				this.$refs.video.webkitRequestFullScreen();
			}
		},
	};
</script>
