					</div>
					<div class="showFY" v-if="gmlist != undefined && gmlist != null && gmlist.length > 0 ">
						<tablePaging :currentPage="currentPage" :total="total" :pageSize="pageSize"
							@pageChange="pageChange" />

					</div>
				</div>
				<div class="personalcenter_five" v-if="navIndex == 2">
					<ul>
						<li>
							<p class="personalcenter_five_name" :class="!openEdit ? 'sizecolor' : ''"><span>*</span>姓名
							</p>
							<input :class="!openEdit ? 'inputal' : ''" type="text" v-model="name" placeholder="请输入姓名"
								:disabled="openEdit">
						</li>
						<li>
							<p class="personalcenter_five_name" :class="!openEdit ? 'sizecolor' : ''"><span>*</span>手机号
							</p>
							<input :class="!openEdit ? 'inputal' : ''" type="text" v-model="tel" placeholder="请输入手机号"
								:disabled="openEdit">
						</li>
						<li>
							<p class="personalcenter_five_name" :class="!openEdit ? 'sizecolor' : ''"><span>*</span>详细地址
							</p>
							<div
								class="personalcenter_five_select w100 flex flex-row flex-nowrap align-start justify-between">

								<el-select v-model="pr" placeholder="请选择" @change='getindex(pr)' :disabled="openEdit"
									:class="!openEdit ? 'inputal' : ''">
									<el-option v-for="item in sflist" :key="item.name" :label="item.name"
										:value="item.name">
									</el-option>
								</el-select>
								<el-select v-model="city" placeholder="请选择" @change='getindex2(city)'
									:disabled="openEdit" :class="!openEdit ? 'inputal' : ''">
									<el-option v-for="item in citylist" :key="item.name" :label="item.name"
										:value="item.name">
									</el-option>
								</el-select>

							</div>
							<input type="text" v-model="address" placeholder="请输入详细地址" :disabled="openEdit"
								:class="!openEdit ? 'inputal' : ''">
						</li>
						<li>
							<p class="personalcenter_five_name" :class="!openEdit ? 'sizecolor' : ''"><span>*</span>头像
							</p>
							<el-upload class="avatar-uploader"
								action="https://redyun.cbcrfund.com/index.php/api/upload/apiuploads"
								:show-file-list="false" :on-success="handleAvatarSuccess"
								:before-upload="beforeAvatarUpload" :disabled="openEdit">
								<img v-if="imageUrl" :src="imageUrl" class="avatar">
								<i v-else class="el-icon-plus avatar-uploader-icon"></i>
								<div slot="tip" class="el-upload__tip">只能上传jpg/png文件，且不超过2M</div>
							</el-upload>
						</li>
						<li>
							<p class="personalcenter_five_name">&nbsp;</p>
							<div class="five_li_btn">
								<button v-if="openEdit" @click="openEdit = false">开启编辑</button>
								<button v-else @click="submit()">确认修改</button>
							</div>
						</li>
					</ul>
				</div>
				<div class="personalcenter_six" v-if="navIndex == 3">
					<ul>
						<li>
							<p class="six_li_name">电话</p>
							<div class="six_li_input"><input type="text" v-model="phoneNum" maxlength="11"
									placeholder="请输入手机号"></div>
						</li>
						<li>
							<p class="six_li_name">&nbsp;</p>
							<div class="six_li_input yam"><input type="text" v-model="sendCode" placeholder="请输入验证码">
							</div>
							<div class="yam-btn">
								<button @click="sendMsg" v-if="sendDisabled">发送验证码</button>
								<button disabled="true" v-else>{{sendTime+"s"}}</button>
							</div>
						</li>
						<li>
							<p class="six_li_name">密码</p>
							<div class="six_li_input"><input type="password" placeholder="请输入密码" v-model="password">
							</div>
						</li>
						<li>
							<p class="six_li_name">确认密码</p>
							<div class="six_li_input"><input type="password" placeholder="请输入确认密码" v-model="reqirPass">
							</div>
						</li>
						<li>
							<p class="six_li_name">&nbsp;</p>
							<div class="six_li_input" @click="subok()"><button>确认</button></div>
						</li>
					</ul>
				</div>
			</div>
		</div>

		<div class="new_footer">
			<div class="new_foot">
				<div class="new_foot_left">
					<ul>
						<li>
							<router-link to="/index">红色足迹</router-link>
						</li>
						<li>|</li>
						<li>
