						</li>
						<li>|</li>
						<li>
							<router-link to="/user">个人中心</router-link>
						</li>
					</ul>
					<p class="new_foot_line"></p>
					<p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
				</div>
				<div class="new_foot_right">
					<div class="foot_weixin">
						<!-- <router-link to="/index"></router-link> -->
						<div class="showlg"></div>
						<div class="show_div">
							<img src="../assets/img/ewm.png" alt="">
						</div>
					</div>
					<div class="foot_tel">
						<!-- <router-link to="/index"></router-link> -->
						<div class="showlg"></div>
						<div class="show_div">
							待更新
						</div>
					</div>
				</div>
			</div>
		</div>
		<el-dialog title="提示" :visible.sync="dialogVisible" width="20%" :close-on-click-modal='false'>
			<span>确定要退出吗</span>
			<span slot="footer" class="dialog-footer">
				<el-button @click="dialogVisible = false">取 消</el-button>
				<el-button type="primary" @click="close()">确 定</el-button>
			</span>
		</el-dialog>
	</div>
</template>

<script>
	import TablePaging from "../components/Fy";
	import api from "../api/api.js";
	import $ from 'jquery';
	import {
		MessageBox,
		Message
	} from 'element-ui';
	export default {
		name: 'personalcenter',
		components: {

			TablePaging,
		},
		data() {
			return {
				isLogin: false,
				// navList: [ '个人资料', '账号安全'],
				navList: ['我的足迹', '我的收藏', '个人资料', '账号安全'],
				navIndex: 2,
				isEdit: false,
				sclb: 1,
				dialogVisible: false,
				phoneNum: '',
				sendCode: '',
				sendTime: 60,
				sendDisabled: true,
				openEdit: true, // 开启个人信息编辑
				sflist: [],
				citylist: [],
				options: [],
				userInfo: {},
				value: '',
				name: '',
				tel: '',
				pr: null,
				city: null,
				address: null,
				imageUrl: '',
				password: '',
				reqirPass: '',
				gmlist: [],
				currentPage: 1,
				total: 0,
				pageSize: 9,
			}
		},
		mounted() {

			$('.personalcenter_four ul li').hover(
				function() {
					$(this).addClass('active');
				},
				function() {
					$(this).removeClass('active');
				}
			);

			if (localStorage.getItem("userinfo")) {
				this.userInfo = JSON.parse(localStorage.getItem("userinfo"))
				this.name = this.userInfo.nickname
				this.tel = this.userInfo.phone
				this.phoneNum = this.userInfo.phone
				this.imageUrl = this.userInfo.headimg
				this.address = this.userInfo.address
				this.showa = true
				console.log(this.userInfo)
			}

			this.getList()

		},
		methods: {
			tourl(url) {
				let routeUrl = this.$router.resolve({
					path: url,
				}) // 带参跳转

				window.open(routeUrl.href, '_blank');
			},
			// 左侧菜单
			navClick(index) {
				this.navIndex = index;
				console.log(this.navIndex)
				if (index == 0) {
					this.tabZJ('venue', 1)
				} else if (index == 1) {
					this.tab('venue', 1)
				}
			},
			close() {
				this.dialogVisible = false

				setTimeout(() => {
					localStorage.clear();
					this.$router.push("/");
				}, 800);
			},
			handleClose(done) {
				this.$confirm('确认关闭？')
					.then(_ => {
						done();
					})
					.catch(_ => {});
			},
			subok() {
				if (!this.phoneNum) {
					this.$message({
						message: '电话不可为空',
						type: "error",
					});
					return false
				} else if (!/^1[3456789]\d{9}$/.test(this.phoneNum)) {
					this.$message({
						message: '请输入正确的电话',
						type: "error",
					});
					return false
				} else if (!this.sendCode) {
					this.$message({
						message: '请输入电话验证码',
						type: "error",
					});
					return false
				} else if (!this.password) {
					this.$message({
						message: '请输入修改的密码',
						type: "error",
					});
					return false
				} else if (!this.reqirPass) {
					this.$message({
						message: '请再次输入密码',
						type: "error",
					});
					return false

				} else if (this.password !== this.reqirPass) {
					this.$message({
						message: '俩次密码不匹对',
						type: "error",
					});
					return false
				} else if (this.password.length < 8) {
					this.$message({
						message: '密码应是8-16位',
						type: "error",
					});
					return false
				} else if (this.password.length > 16) {
					this.$message({
						message: '密码应是8-16位',
						type: "error",
					});
					return false
				} else {
					var that = this
					this.$http
						.post(api.user.accountSecurity, {
							phone: that.phoneNum,
							setPasscode: that.sendCode,
							password: that.password,
							reqirPass: that.reqirPass,


						})
						.then((res) => {
							if (res.data.code == 0) {
								this.$message({
									message: '修改成功',
									type: "success",
								});
							} else {
								this.$message({
									message: res.data.res,
									type: "error",
								});
							}
						});
				}
			},
			pageChange(value) {
				console.log(value, "hhhh");
				this.currentPage = value;
				console.log(this.sclb)
				if (this.navIndex == 1) {
					if (this.sclb == 1) {
						this.tab('venue', 1)
					} else if (this.sclb == 2) {
						this.tab('digitalvenue', 2)
					} else if (this.sclb == 3) {
						this.tab('education', 3)
					} else if (this.sclb == 4) {
						this.tab('exhibition', 4)
					}
				} else if (this.navIndex == 0) {
					if (this.sclb == 1) {
						this.tabZJ('venue', 1)
					} else if (this.sclb == 2) {
						this.tabZJ('digitalvenue', 2)
					} else if (this.sclb == 3) {
						this.tabZJ('education', 3)
					} else if (this.sclb == 4) {
						this.tabZJ('exhibition', 4)
					}
				}

			},
			getindex(item) {
				console.log(item)
				this.pr = item
				this.sflist.forEach((i, x) => {
					if (i.name == item) {
						this.city = ''
						this.citylist = i.city
					}
				});
			},
			tab(type, index) {
				this.sclb = index
				var that = this
				this.gmlist = [],
					this.currentPage = 1
				this.$http
					.post(api.user.UserCollect, {
						module: type,
						limit: this.pageSize,
						page: this.currentPage
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							that.gmlist = res.data.data.data
							that.total = res.data.data.totalNum
						} else if (res.data.code == -100) {
							localStorage.clear();
							this.$router.push("/user");
						} else {
							// this.$message({
							//   message: res.data.msg,
							//   type: "error",
							// });
						}
					});
			},
			tabZJ(type, index) {
				this.sclb = index
				var that = this
				this.gmlist = [],
					this.currentPage = 1
				this.$http
					.post(api.user.UserBrowHistory, {
						module: type,
						limit: this.pageSize,
						page: this.currentPage
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							that.gmlist = res.data.data.HistoryList
							that.total = res.data.data.totalNum
						} else if (res.data.code == -100) {
							localStorage.clear();
							this.$router.push("/user");
						} else {
							// this.$message({
							//   message: res.data.msg,
							//   type: "error",
							// });
						}
					});
			},
			getindex2(item) {
				this.city = item
			},
			gotoxq1(id, item, title) {
				console.log(title, "kkkk")
				if (title) {
					this.$router.push({
						path: item,
						query: {
							id: id,
							title: title
						}
					});
				} else {
					this.$router.push({
						path: item,
						query: {
							id: id
						}
					});
				}

			},

			submit() {
				if (this.name == '') {
					this.$message({
						message: '姓名不可为空',
						type: "error",
					});
					return false
				} else if (this.tel == '') {
					this.$message({
						message: '手机号不可为空',
						type: "error",
					});
					return false
				} else if (!/^1[3456789]\d{9}$/.test(this.tel)) {
					this.$message({
						message: '请输入正确的手机号',
						type: "error",
					});
					return false
				} else if (this.pr == null) {
					this.$message({
						message: '请选择省份',
						type: "error",
					});
					return false
				} else if (this.city == '') {
					this.$message({
						message: '请选择城市',
						type: "error",
					});
					return false
				} else if (this.address == null) {
					this.$message({
						message: '请填写详细地址',
						type: "error",
					});
					return false
				} else if (this.imageUrl == '') {
					this.$message({
						message: '请上传头像',
						type: "error",
					});
					return false
				} else {
					var that = this
					this.$http
						.post(api.user.UserInfoEdit, {
							nickname: this.name,
							phone: this.tel,
							province: this.pr,
							city: this.city,
							headimg: this.imageUrl,
							address: this.address
						})
						.then((res) => {
							console.log(res)
							if (res.data.code == 0) {
								this.$message({
									message: '编辑成功',
									type: "success",
								});
								localStorage.setItem("userinfo", JSON.stringify(res.data.data.userinfo));
								// setTimeout(() => {
								// 	location.reload();
								// }, 1000);
							} else {
								this.$message({
									message: res.data.msg,
									type: "error",
								});
							}
						});
				}
			},
			getList() {
				this.$http
					.post(api.user.sf, {

					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {

							this.sflist = res.data.data
							if (this.showa) {
								this.pr = this.userInfo.province
								this.sflist.forEach((i, x) => {
									if (i.name == this.pr) {
										this.citylist = i.city
										this.city = this.userInfo.city

									}
								});
							}
						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			yichu(id) {
				var nice = ''
				if (this.sclb == 1) {
					nice = 'venue'
				} else if (this.sclb == 2) {
					nice = 'digitalvenue'
				} else if (this.sclb == 3) {
					nice = 'education'
				} else if (this.sclb == 4) {
					nice = 'exhibition'
				}
				var that = this
				this.$http
					.post(api.user.UserDelCollect, {
						module: nice,
						id: id
					})
					.then((res) => {
						console.log(res)
						if (res.data.code == 0) {
							this.$message({
								message: '移除成功',
								type: "success",
							});
							if (that.sclb == 1) {
								that.tab('venue', 1)
							} else if (that.sclb == 2) {
								that.tab('digitalvenue', 2)
							} else if (that.sclb == 3) {
								that.tab('education', 3)
							} else if (that.sclb == 4) {
								that.tab('exhibition', 4)
							}

						} else {
							this.$message({
								message: res.data.msg,
								type: "error",
							});
						}
					});
			},
			// 获取验证码
			sendMsg: function() {
				let that = this;
				if (that.phoneNum === '') {
					Message({
						message: '请输入手机号码',
						type: 'warning',
						center: true,
						offset: 130,
						duration: 1000
					});
					return false;
				} else if (!(/^1[3456789]\d{9}$/.test(that.phoneNum))) {
					Message({
						message: '请填写正确的手机号',
						type: 'warning',
						center: true,
						offset: 130,
						duration: 1000
					});
					return false;
				}
				if (that.sendDisabled == false) {
					return false
				} else {
					that.send()
				}
				that.countdown()
				that.sendDisabled = false;
			},
			// 发送验证码
			send() {
				var that = this
				this.$http
					.post(api.user.SendMsgCode, {
						phone: that.tel,
						scene: 'setPass'
					})
					.then((res) => {
						if (res.data.code == 0) {
							this.$message({
								message: '发送成功',
								type: "success",
							});
						} else {
							this.$message({
								message: res.data.res,
								type: "error",
							});
						}
					});
			},
			// 倒计时
			countdown: function() {
				var promise = new Promise((resolve, reject) => {
					var setTimer = setInterval(() => {
						this.sendTime--
						if (this.sendTime <= 0) {
							this.sendTime = 60;
							this.sendDisabled = true
							resolve(setTimer)
						}
					}, 1000)
				})
				promise.then((setTimer) => {
					clearInterval(setTimer)
				})
			},


			handleAvatarSuccess(res, file) {
				console.log(res, file)
				this.imageUrl = res.data;
			},
			beforeAvatarUpload(file) {
				const isJPG = file.type === 'image/jpeg' || file.type === 'image/png'
				const isLt2M = file.size / 1024 / 1024 < 10;

				if (!isJPG) {
					this.$message.error('上传头像图片只能是 JPG/png/gif格式!');
				}
				if (!isLt2M) {
					this.$message.error('上传头像图片大小不能超过 2MB!');
				}
				return isJPG && isLt2M;
			},
			// 去登录注册
			goToUser() {
				if (localStorage.getItem("userinfo")) {
					this.$router.push({
						path: '/personalcenter'
					});

				} else {
					this.$router.push({
						path: '/user'
					});
				}

			},

		},
	}
</script>

