<template>
  <div class="venue" :style="'background:#f5f5f5 url('+banner+') no-repeat center top;'">
    <div class="new_header header_brotop">
      <div class="flex flex-row flex-nowrap align-center space-between w1400">
        <div class="logo" ref="logo"></div>
        <div class="menu-bar">
          <ul>
            	<li  @click="tourl('/index')"><a>首页</a></li>
            	<li @click="tourl('/groupexhibition')"><a>数字联展</a></li>
            			<li @click="tourl('/groupexhibitionnews')"><a>联展资讯</a></li>
            	<li class="active" @click="tourl('/digitalvenue')"><a>数字传播</a></li>
            	<li @click="tourl('/venue')"><a>革命场馆</a></li>
            	<li @click="tourl('/personalcenter')"><a>个人中心</a></li>
          </ul>
        </div>
        <!-- <div class="new_user"></div> -->
      </div>
    </div>
    <div class="bread_crumbs flex flex-nowrap">
      <div class="br_left"><img src="../assets/img/crumbs_1.png"></div>
      <div class="br_line"></div>
      <div class="br_right">
        <h2>数字传播</h2>
        <div class="br_ul">
          <span><img src="../assets/img/crumbs_2.png"></span>
          <span>
            <router-link to="/index">首页</router-link>
          </span>
          <span>/</span>
          <span>数字传播</span>
        </div>
      </div>
    </div>

    <div class="digitalvenue-one">
      <ul class="flex flex-row flex-nowrap align-start justify-between">
        <li v-for="(item, index) in list1" @click="Anchor(`#anchor${index}`)">
          <p class="number">0{{index+1}}</p>
          <p class="title">{{item.title}}</p>
        </li>
      </ul>
    </div>
    <div class="digitalvenue-two flex flex-row flex-nowrap align-start justify-between" style="cursor: pointer;">
      <div class="digitalvenue-two-left" v-for="(item, index) in videoone" :key='index' >
        <div class="digitalvenue-two-left-img" :style="'background:url('+item.cover+') center no-repeat;'" @click="goTodetail(item.id)">

          <!-- <img :src="item.cover" alt=""> -->
        </div>
        <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
            <p>收藏</p>
          </span><span class="el-icon-star-on" v-else>
            <p>已收藏</p>
          </span></div>
        <!--   <p class="digitalvenue-two-left-btn" ><img src="../assets/img/btn-video.png" alt=""></p> -->
      </div>
      <ul class="flex flex-row flex-wrap justify-between align-start " style="cursor: pointer;">
        <li v-for="(items, indexs) in videolist"  class="diright">
          <div class="digitalvenue-li-img" :style="'background:url('+items.cover+') center no-repeat;'" @click="goTodetail(items.id)">
            <!-- <img :src="items.cover" alt=""> -->

          </div>
          <div class="prsc" @click="shouc(items)" v-if="showli"><span class=" el-icon-star-off" v-if="items.is_collect=='false'">
              <p>收藏</p>
            </span><span class="el-icon-star-on" v-else>
              <p>已收藏</p>
            </span></div>
          <!-- <p class="digitalvenue-li-btn"><img src="../assets/img/btn-video.png" alt=""></p> -->
        </li>
      </ul>
    </div>
    <div class="digitalvenue-three">
      <div class="digitalvenue-header">
        <img src="../assets/img/video.png" alt="">
        <span class="digitalvenue-header-1">视频</span>
        <span class="digitalvenue-header-2">共有<span class="text-c72c2c">{{num}}</span>个视频</span>
      </div>
      <div class="digitalvenue-list" v-for="(item, index) in datas" :id="`anchor${index}`">
        <div class="digitalvenue-title">
          <div class="digitalvenue-title-left">
            <span class="digitalvenue-title-1">{{item.category.title}}</span>
            <span class="digitalvenue-title-2">共有<span class="text-c72c2c">{{item.count}}</span>个视频</span>
          </div>
          <div class="digitalvenue-title-right" @click="gotoxq(item.category.id)">查看更多</div>
        </div>
        <div class="digitalvenue-ul">
          <ul class="flex flex-row flex-wrap align-start justify-start">
            <li v-for="(items, indexs) in item.videoList" >
              <div class="digitalvenue-ul-img showulung">
                <!-- <img :src="items.cover" > -->
                <div class="fm" :style="'background:url('+items.cover+') center no-repeat;'" @click="goTodetail(items.id)">


                </div>
                <div class="prsc" @click="shouc(items)" v-if="showli"><span class=" el-icon-star-off" v-if="items.is_collect=='false'">
                    <p>收藏</p>
                  </span><span class="el-icon-star-on" v-else>
                    <p>已收藏</p>
                  </span></div>
              </div>
              <div class="digitalvenue-ul-title">
                <p>{{items.title}}</p>
                <img src="../assets/img/dig-play.png">
              </div>
            </li>
          </ul>
        </div>
      </div>
    </div>

    <div class="new_footer">
      <div class="new_foot">
        <div class="new_foot_left">
      <ul>
        <li>
          <router-link to="/index">红色足迹</router-link>
        </li>
        <li>|</li>
        <li>
          <router-link to="/groupexhibition">数字联展</router-link>
        </li>
        <li>|</li>
        <li>
          							<router-link to="/groupexhibitionnews">联展资讯</router-link>
        </li>
        <li>|</li>
        <li>
          <router-link to="/digitalvenue">数字联播</router-link>
        </li>
        <li>|</li>
        <li>
          <router-link to="/venue">革命场馆</router-link>
        </li>
        <li>|</li>
        <li>
          <router-link to="/user">个人中心</router-link>
        </li>
      </ul>
          <p class="new_foot_line"></p>
          <p class="new_foot_beian">© 2021 中国文物信息咨询中心 版权所有　备案号：京ICP备05047720号-2</p>
        </div>
        <div class="new_foot_right">
          <div class="foot_weixin">
            <router-link to="/index"></router-link>
          </div>
          <div class="foot_tel">
            <router-link to="/index"></router-link>
          </div>
        </div>
      </div>
    </div>

    <div class="videoBox" v-if="showvideo">
      <div class="close" @click="showvideo = false"></div>
      <video controls="controls" autoplay="autoplay" :src="video"></video>
    </div>

  </div>
</template>

<script>
  import api from "../api/api.js";
  import $ from 'jquery';
  export default {
    name: 'venue',
    data() {
      return {
        scroll: 0,
        list1: ['红色印记', '事件的答卷', '闪光的记忆', '追光之路', '红色文物100'],
        showvideo: false,
        datas: [],
        videolist: [],
        videoone: [],
        video: '',
        num: '',
        showli: true,
				banner:'',
      }
    },
    mounted() {
			if (localStorage.getItem("userinfo")) {
			  this.showli = true
			}else{
							this.showli = false
					}
      // 添加滚动监听事件
      window.addEventListener('scroll', this.handleScroll);

      // 顶部
      $('.digitalvenue-one ul li').hover(
        function() {
          $(this).addClass('active').stop(true, true);
        },
        function() {
          $(this).removeClass('active').stop(true, true);
        }
      );
      this.getlist()
    },
    methods: {
      openvideo(item) {
        this.showvideo = true
        this.video = item.video
      },
      gotoxq(id) {
        this.$router.push({
          path: '/digitalvenuelist',
          query: {
            id: id
          }
        });
      },
      getlist() {
        var that = this
        this.$http
          .post(api.user.VideoList, {

          })
          .then((res) => {
            console.log(res.data.data)
            if (res.data.code == 0) {
              that.list1 = res.data.data.categoryList
              that.datas = res.data.data.data
              that.videolist = res.data.data.TOPSmall
              that.videoone = res.data.data.TOPBig
              that.num = res.data.data.Allcount
			that.banner = res.data.data.banner
            } else if (res.data.code == -100) {
              localStorage.clear();
              that.getlist()
							that.showli = false
              // this.$router.push("/user");
            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      // 导航滚动
      handleScroll() {
        this.scroll = document.documentElement.scrollTop || document.body.scrollTop
        if (this.scroll > 150) {
          $('.new_header').addClass('active');
        } else {
          $('.new_header').removeClass('active');
        }
      },
      // 锚点点击
      Anchor(selector) {
        document.querySelector(selector).scrollIntoView({
          behavior: "smooth",
          block: "center",
          inline: "nearest"
        });
      },
      // 跳转详情
      goTodetail(id) {
        this.$router.push({
          path: '/digitalvenuedetail',
          query: {
            id: id
          }
        });
        // this.$router.push({ path: '/invalidation', query:{ token: this.token}});
      },
    tourl(url){
      let routeUrl = this.$router.resolve({
                path:url,
              }) // 带参跳转

              window.open(routeUrl.href, '_blank');
    },
 shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },

      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'digitalvenue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'digitalvenue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
    },
    //销毁滚动事件,否则跳到别的路由还是会出现
    destroyed() {
      window.removeEventListener('scroll', this.handleScroll)
    }
  }
</script>

<style scoped>
  ul li{
    cursor: pointer;
  }
  .venue {
    width: 100%;
    height: auto;
    background: #f5f5f5 url(../assets/img/digitalvenue-bg.jpg) no-repeat center top;
    background-size: 100% auto;
  }

  .bread_crumbs {
    width: 1400px;
    margin: 0 auto;
    padding-top: 190px;
  }

  .digitalvenue-one {
    width: 1330px;
    height: 100px;
    background-color: #fff;
    margin: 80px auto 0;
  }

  .digitalvenue-one ul li {
    width: 20%;
    height: 100px;
    position: relative;
    cursor: pointer;
  }

  .digitalvenue-one ul li .number {
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 1;
    color: rgba(255, 255, 255, .1);
    font-size: 80px;
    font-weight: bold;
  }

  .digitalvenue-one ul li .title {
    width: 100%;
    height: 100px;
    line-height: 100px;
    text-align: center;
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    z-index: 2;
    font-size: 20px;
    color: rgba(0, 0, 0, .6);
  }

  .digitalvenue-one ul li.active {
    background-color: #c72c2c;
  }

  .digitalvenue-one ul li.active .title {
    color: #fff;
  }

  .digitalvenue-two {
    width: 1400px;
    margin: 40px auto 0;
  }

  .digitalvenue-two-left {
    width: 680px;
    height: 484px;
    position: relative;
  }

  .digitalvenue-two-left-img {
    width: 100%;
    height: 100%;
    overflow: hidden;
    background-size: cover !important;
  }

  .digitalvenue-two-left:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .digitalvenue-two-left-img img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-two-left-btn {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    z-index: 1;
    cursor: pointer;
  }

  .digitalvenue-two-left-btn img {
    position: absolute;
    width: 99px;
    height: 99px;
    z-index: 2;
    top: calc(50% - 45px);
    left: calc(50% - 45px);
  }

  .digitalvenue-two ul {
    width: 690px;
    height: 484px;
    align-content: space-between;
  }

  .digitalvenue-two ul li {
    width: 332px;
    height: 230px;
    position: relative;
  }

  .digitalvenue-two ul li .digitalvenue-li-img {
    width: 332px;
    height: 230px;
    overflow: hidden;
    background-size: cover !important;
    position: relative;
  }

  .prsc {

    background: rgba(0, 0, 0, 0.5);
    width: 80px;
    height: 32px;
    line-height: 32px;
    position: absolute;
    top: 20px;
    right: 20px;
    color: #FFFFFF;
    font-size: 16px;
    display: flex;
    align-items: center;
    border-radius: 3px;
    justify-content: center;
    cursor: pointer;
    opacity: 0;

  }
  .showulung{
    position: relative;
  }
  .digitalvenue-li-img:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .diright:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .showulung:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF;
    font-size: 16px;
  }
  .digitalvenue-two ul li .digitalvenue-li-img img {
    width: 100%;
    height: 100%;
  }

  .digitalvenue-two ul li .digitalvenue-li-btn {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, .3);
    z-index: 1;
    cursor: pointer;
  }

  .digitalvenue-two ul li .digitalvenue-li-btn img {
    position: absolute;
    width: 60px;
    height: 60px;
    z-index: 2;
    top: calc(50% - 30px);
    left: calc(50% - 30px);
  }

  .digitalvenue-three {
    width: 1400px;
    margin: 0 auto;
    padding-top: 60px;
  }

  .digitalvenue-header {
    width: 100%;
    padding-bottom: 23px;
    border-bottom: 1px solid #adadad;
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
    justify-content: flex-start;
  }

  .digitalvenue-header img {
    width: 57px;
    height: 45px;
  }

  .digitalvenue-header-1 {
    font-size: 36px;
    color: #000;
    margin-left: 15px;
  }

  .digitalvenue-header-2 {
    font-size: 16px;
    color: #666;
    margin-left: 20px;
  }

  .digitalvenue-list {
    width: 100%;
  }

  .digitalvenue-title {
    width: 100%;
    margin-top: 50px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-title-left {
    display: flex;
    flex-flow: row nowrap;
    align-items: flex-end;
  }

  .digitalvenue-title-1 {
    font-size: 30px;
    color: #000;
  }

  .digitalvenue-title-2 {
    font-size: 16px;
    color: #666;
    margin-left: 20px;
  }

  .digitalvenue-title-right {
    background: url(../assets/img/dig-more.png) no-repeat right center;
    background-size: 14px 19px;
    padding-right: 30px;
    margin-right: 10px;
    font-size: 18px;
    cursor: pointer;
  }

  .digitalvenue-title-right a {
    color: #c71925;
  }

  .digitalvenue-title-right a:hover {
    color: #c71925;
    opacity: .9;
  }

  .digitalvenue-ul {
    width: 100%;
    border-bottom: 1px solid #adadad;
    padding-bottom: 40px;
  }

  .digitalvenue-ul ul li {
    width: 23.5%;
    margin-right: 2%;
    overflow: hidden;
    margin-top: 30px;
    cursor: pointer;
  }

  .digitalvenue-ul ul li:nth-child(4n) {
    margin-right: 0px;
  }

  .digitalvenue-ul-img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-img img {
    width: 100%;
    height: auto;
  }

  .digitalvenue-ul-img .fm {
    width: 100%;
    height: 235px;
    background-size: cover !important;
    position: relative;
  }

  .digitalvenue-ul-img .fm:hover>.prsc {
    z-index: 1;
    opacity: 1;
  }

  .digitalvenue-ul-title {
    width: 100%;
    height: 60px;
    display: flex;
    flex-flow: row nowrap;
    align-items: center;
    justify-content: space-between;
  }

  .digitalvenue-ul-title p {
    width: calc(100% - 24px);
    font-size: 18px;
    color: #000;
    white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis;
  }

  .digitalvenue-ul-title img {
    width: 24px;
    height: 24px;
  }
</style>



