            <p><img src="../assets/img/v3.jpg" alt=""></p>
            <p>建设中国共产党历史展览馆，举办中国共产党历史展览，是以习近平同志为核心的党中央做出的重大决策，是中国共产党成立 100 周年庆祝活动的重要内容，党史展览馆和“不忘初心、牢记使命”中国共产党历史展览于2021年“七一”前夕正式开馆、开展。</p>
            <p>中国共产党历史展览馆位于朝阳区北辰东路9号。党史展览馆整体为“工”字形建筑，建筑南北长201米、东西宽81米、高50米。外观采用传统柱廊式结构，充分传承中华建筑方正端庄、厚重大气的传统精髓。广场上大型党旗雕塑和以“伟大斗争、伟大工程、伟大事业、伟大梦想” 为主题的四组群雕，与建筑风格有机融合，彰显红色精神内涵。</p>
            <p>党史展览馆主体建筑总面积14.7万平方米，地上7层，地下3层，其中地上1至3层为党史基本陈列展厅，4层和地下1层为专题展厅。党史展览馆配套设施建有21个电影放映厅和书店及商业餐饮设施，多方面满足观众需求。</p> -->


          </div>
