      <ul v-if="alllist.recommend != undefined && alllist.recommend != null && alllist.recommend.length > 0">
        <li v-for="(item,index) in alllist.recommend" @click="goToDetail(item.id,item.cover)">
          <div class="prsc" @click="shouc(item)" v-if="showli"><span class=" el-icon-star-off" v-if="item.is_collect=='false'">
              <p>收藏</p>
            </span><span class="el-icon-star-on" v-else>
              <p>已收藏</p>
            </span></div>
