  </div>
</template>

<script>
  import api from "../api/api.js";
  import $ from 'jquery';
  export default {
    name: 'venuedetail',
    data() {
      return {
        alllist: {},
        id: '',
        showli:true
      }
    },
    mounted() {
      this.id = this.$route.query.id
			if (localStorage.getItem("userinfo")) {
			    this.showli = true
			  }else{
					this.showli = false
			}
      this.getlist()
      $('.detail_recommend ul li').hover(
        function() {
          $(this).addClass('active');
        },
        function() {
          $(this).removeClass('active');
        }
      );
    },
    methods: {
      tourl(url){
        let routeUrl = this.$router.resolve({
                  path:url,
                }) // 带参跳转

                window.open(routeUrl.href, '_blank');
      },
      // 跳转详情
      getlist() {
        var that = this
        this.$http
          .post(api.user.StadiumInfo, {
            Stadium_id: this.id,
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              that.alllist = res.data.data
              that.alllist.building_describe = that.alllist.building_describe.replace(/\<img/gi,
                '<img style="max-width:100%;height:auto;margin:auto" ')
              that.alllist.building_describe = that.alllist.building_describe.replace(/\<p/gi,
                '<p style="font-size: 18px; color: #000; padding:10px 0; line-height: 30px; text-indent:24px;" ')

            } else {
              this.$message({
                message: res.data.res,
                type: "error",
              });
            }
          });
      },
      gourl(url) {
        // window.location.href = url;
        window.open(url, '_blank');
      },
      shouc(item) {
        console.log(item, "剪辑")
        if (item.is_collect == "true") {
          this.nosc(item.id)
        } else {
          this.oksc(item.id)
        }
      },

      oksc(id) {
        var that = this
        this.$http
          .post(api.user.UserAddCollect, {
            module: 'venue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '收藏成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      nosc(id) {
        var that = this
        this.$http
          .post(api.user.UserDelCollect, {
            module: 'venue',
            id: id
          })
          .then((res) => {
            console.log(res)
            if (res.data.code == 0) {
              this.$message({
                message: '取消成功',
                type: "success",
              });
              that.getlist()

            } else {
              this.$message({
                message: res.data.msg,
                type: "error",
              });
            }
          });
      },
      goToDetail(id,img) {
        this.$router.push({
          path: '/groupexhibitiondetail',
          query: {
            id: id,
			img:img
          }
        });
      }
    },
  }
</script>

<style scoped>
  .brrder_bom1 {
    border-bottom: 1px solid #b5001f;
  }

  .brrder_bom1.recent {
    width: 100%;
  }
