    cursor: pointer;
    opacity: 0;

  }

  .detail_recommend ul li:hover>.prsc {
    z-index: 1;
        opacity: 1;
  }

  .prsc span {
    font-size: 20px;
    color: #ccc;
    padding-right: 5px;
    display: flex;
    align-items: center;
  }

  .prsc span p {
    color: #FFFFFF;
    font-size: 16px;
  }

  .venue_two ul li.active .prsc span {
    z-index: 1;
  }

  .detail_recommend ul li:nth-child(4n) {
    margin-right: 0;
  }

  .detail_recommend ul li .venue_two_img {
